import { Component, OnInit } from '@angular/core';
import { ProductService } from 'src/app/Utilities/services/products/product.service';
import { UILoaderService } from 'src/app/Utilities/services/ui-loader/ui-loader.service';
import { GlobalModelsService } from 'src/app/Utilities/services/global/global-models.service';
import { ProductSearchService } from 'src/app/Utilities/services/products/product-search.service';

@Component({
  selector: 'app-sort-filter-block',
  templateUrl: './sort-filter-block.component.html',
  styleUrls: ['./sort-filter-block.component.css']
})
export class SortFilterBlockComponent implements OnInit {

  constructor(
    private productService: ProductService,
    private uiLoaderService: UILoaderService,
    private globalModelsService: GlobalModelsService,
    private productSearchService: ProductSearchService
  ) { }

  globalModelsLoaded = false

  leftSteps = 4;
  rightSteps = 4;
  
  priceDropMinIndex = 0;
  priceDropMaxIndex = 4;

  priceDropMin: string = '0';
  priceDropMax: string = '100000';

  _main_priceMinValues = ['0', '1500', '3000', '5000', '10000'];
  _main_priceMaxValues = ['1500', '3000', '5000', '10000', '100000'];

  priceMinValues = ['0', '1500', '3000', '5000', '10000'];
  priceMaxValues = ['1500', '3000', '5000', '10000', '100000'];

  allFilters: any = new Map<string, string[]>();

  catergories = [];
  materials = [];
  section = [];
  metalPurity = [];
  offers = [];

  allSort = ['Newly Listed', 'Price - High to Low', 'Price - Low to High', 'Discount'];

  
  selectedFilters = new Map<string, string[]>();
  selectedSort: string = '';

  ngOnInit(): void {

    if(this.globalModelsService.globalModelsLoaded) {
      this.loadComponentData()
      this.globalModelsLoaded = this.globalModelsService.globalModelsLoaded;
    }

    this.globalModelsService.globalModelsLoadedObservable.subscribe(
      (globalModelsLoaded) => {
        if(globalModelsLoaded) {
          this.loadComponentData();
        }
        this.globalModelsLoaded = globalModelsLoaded;
      }
    )
  }

  loadComponentData() {
    this.catergories = this.globalModelsService.getCategoryNamesAsList()
    this.materials = this.globalModelsService.getMaterialNamesAsList()
    this.section = this.globalModelsService.getSectionNamesAsList()
    this.metalPurity = this.globalModelsService.getMetalPurtiesAsList()
    this.offers = this.globalModelsService.getDiscountPercentsAsList()

    console.log('Categories filter ==> ', this.catergories)

    this.allFilters.set('Categories', this.catergories);
    this.allFilters.set('Section', this.section);
    this.allFilters.set('Materials', this.materials);
    this.allFilters.set('Metal Purity', this.metalPurity);
    this.allFilters.set('Offers', this.offers);

    this.selectedSort = this.allSort[0];
  }

  checkIfSelected(key: string, checkValue: string) {
    if (this.selectedFilters.size <= 0)
      return false;

    if(this.selectedFilters.has(key))
      return this.selectedFilters.get(key).includes(checkValue);
    
    return false;

  }

  addToFilter(key: string, value: string) {
    if(!this.selectedFilters.has(key)) {
      this.selectedFilters.set(key, [value]);
    }
    else if(!this.checkIfSelected(key, value)) {
      let values = this.selectedFilters.get(key);
      values.push(value);
      this.selectedFilters.set(key, values);
    } else if(this.checkIfSelected(key, value)) {
      this.deleteFromFilter(key, value);
    }

  }
  
  deleteFromFilter(key: string, value: string) {

    if (this.selectedFilters.has(key)) {
      let values = this.selectedFilters.get(key);

      if(values.length === 1) {
        this.selectedFilters.delete(key)
      } else {
        const index = values.indexOf(value, 0);
        if (index > -1) {
          values.splice(index, 1);
        }
        this.selectedFilters.set(key, values);
      }

    }
  }

  deleteFilter(key: string) {
    if (this.selectedFilters.has(key))
      this.selectedFilters.delete(key)
  }

  resetAllFilters() {
    this.selectedFilters = new Map<string, string[]>();
    this.selectedSort = this.allSort[0];
  }

  setSortCriteria(item: string) {
    this.selectedSort = item;
    this.productService.sortCurrentProductsObserver.next(item);
  }

  validatePriceValues() {
    const maxNumber = this._main_priceMaxValues.indexOf(this.priceDropMax, 0);
    const minNumber = this._main_priceMinValues.indexOf(this.priceDropMin, 0);

    this.priceMinValues = this._main_priceMinValues.slice(0, maxNumber+1)
    this.priceMaxValues = this._main_priceMaxValues.slice(minNumber)
  }

  applyAllFilters() {

    // UI Loader is started before calling for products
    console.log(this.uiLoaderService.apiLoadingScreen + ' set to ' + true)
    this.uiLoaderService.loaderObservable.next([this.uiLoaderService.apiLoadingScreen, true]);
    const globalProductDetails = this.productSearchService.prepareGlobalProductDetailsForFilter(
      this.selectedFilters,
      this.selectedSort,
      this.priceDropMin,
      this.priceDropMax
    );

    this.productService.fetchProductsBasedOnFilter(globalProductDetails);
  }

  getTitle(collection: string) {
    return 'Remove ' + collection;
  }

}
 