import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable()
export class NotificationService {

    notifId = 0;

    NOTIF_INFO = 'NOTIF-INFO'
    NOTIF_SUCCESS = 'NOTIF_SUCCESS'
    NOTIF_WARNING = 'NOTIF_WARNING'
    NOTIF_DANGER = 'NOTIF_DANGER'

    /**
     * [id, type, message] --> only use this observable for notifications
     */
    notificationObservable = new Subject<string[]>();

    // These are for internal calls
    removeNotificationObservable = new Subject<string>();
    notificationReArrangeObservable = new Subject<boolean>();

    getNewId() {
        return '' + this.notifId++;
    }

    decrementNotificationCount() {
      if(this.notifId > 0)
        this.notifId--;
      return this.notifId;
    }

    pushNotification(type: string, message: string) {
      this.notificationObservable.next( [ this.getNewId(), type, message ] )
    }
}