import { Component, OnInit, ViewChild, ElementRef, AfterViewInit, ChangeDetectorRef, Input } from '@angular/core';
import { Guest } from 'src/app/Utilities/models/users/guest.model';
import { CheckOutService } from 'src/app/Utilities/services/products/orders/check-out.service';
import { UserService } from 'src/app/Utilities/services/users/user.service';
import { Customer } from 'src/app/Utilities/models/users/customer.model';
import { ChargeRequest } from 'src/app/Utilities/models/products/orders/charge-request.mode';

declare var Stripe: stripe.StripeStatic;

@Component({
  selector: 'app-stripe-block',
  templateUrl: './stripe-block.component.html',
  styleUrls: ['./stripe-block.component.css']
})
export class StripeBlockComponent implements OnInit, AfterViewInit {

  constructor(
    private checkOutService: CheckOutService,
    private userService: UserService
  ) { }

  @Input() totalAmount = 0
  @Input() currency = 'USD'
  @Input() stripePublicKey = ''

  userLoggedIn = false;

  customerDetails: Customer

  userAddressID = 0;
  guest: Guest;
  paymentReady = false;

  @ViewChild('cardElement') cardElement: ElementRef;

  stripe: stripe.Stripe;
  card;
  
  cardErrors;

  loading = false;
  confirmation;

  ngOnInit(): void {
    this.userLoggedIn = this.userService.customerLoggedIn

    if(this.userLoggedIn) {
      this.customerDetails = this.userService.getCustomerDetails()
    }

    this.userService.customerLoggedInObserver.subscribe (
      (localUserLoggedIn) => {
        this.userLoggedIn = localUserLoggedIn

        if(this.userLoggedIn)
          this.customerDetails = this.userService.getCustomerDetails()
      }
    )

  }
    
  ngAfterViewInit(): void {
    console.log(this.cardElement)
  
    this.stripe = Stripe(this.stripePublicKey);
    const elements = this.stripe.elements();
    this.card = elements.create('card');

    this.card.mount(this.cardElement.nativeElement);

    this.card.addEventListener('change', ({ error }) => {
        this.cardErrors = error && error.message;
    });

  }

  async handleForm(e) {
    e.preventDefault();

    if(this.paymentReady) {

      let email = ''
      if(this.guest != null && !this.userLoggedIn) {
        email = this.guest.emailAddress
      } else {
        email = this.customerDetails.emailAddress
      }

      const { token, error } = await this.stripe.createToken(
        this.card,
        {
          name: email,
          currency: this.currency
        }
      );
  
      if (error) {
        this.cardErrors = error.message;
      } else {
        console.log('Token response ==> ', token)
        let chargeRequest = new ChargeRequest('' , this.totalAmount ,'' ,'' ,'' , 0, null)
        if(this.userLoggedIn)
          chargeRequest = new ChargeRequest('', this.totalAmount, '', email, token.id, this.userAddressID, null)
        else
          chargeRequest = new ChargeRequest('', this.totalAmount, '', email, token.id, 0, this.guest)

          this.checkOutService.chargeCustomer(chargeRequest)
      }
    }
    
  }

  readyPayment(paymentStatus: boolean) {
    this.paymentReady = paymentStatus
    console.log('payment status ====> ',  this.paymentReady)
  }

  getGuest(guestDetails: Guest) {
    this.guest = guestDetails
  }

  getUserAddressID(addressID: number) {
    this.userAddressID = addressID
    console.log('Address ID ====> ', this.userAddressID)
  }


}
