import { Component, OnInit, Renderer2, AfterViewInit, Input, ViewChild } from '@angular/core';
import { Product } from 'src/app/Utilities/models/products/product.model';
import { ProductService } from 'src/app/Utilities/services/products/product.service';
import { ModalDirective } from 'angular-bootstrap-md';
import { ProductKartService } from 'src/app/Utilities/services/products/orders/product-kart.service';
import { Router } from '@angular/router';
import { UserService } from 'src/app/Utilities/services/users/user.service';
import { WishListService } from 'src/app/Utilities/services/products/orders/wish-list.service';

@Component({
  selector: 'app-best-selling-block',
  templateUrl: './best-selling-block.component.html',
  styleUrls: ['./best-selling-block.component.css']
})
export class BestSellingBlockComponent implements OnInit, AfterViewInit {

  constructor(
    private renderer: Renderer2,
    private productService: ProductService,
    private productKartService: ProductKartService,
    private router: Router,
    private userService: UserService,
    private wishListService: WishListService
  ) {}

  components = [];

  @Input() bestSellers: Product[] = [];
  @Input() topProducts: Product[] = [];

  productItem: Product;
  showProduct: boolean = false;

  isUserLoggedIn = false

  @ViewChild('basicModal') basicModal: ModalDirective;
  
  ngOnInit(): void {

    this.isUserLoggedIn = this.userService.customerLoggedIn

    this.userService.customerLoggedInObserver.subscribe(
      (localCustomerLoggedIn) => this.isUserLoggedIn = localCustomerLoggedIn
    )

    this.wishListService.wishListLoadedObservable.subscribe(
      (wishListLoaded) => {
        if(wishListLoaded) {
          this.bestSellers.forEach(
            (p: Product) => p.isWishListed = this.wishListService.isProductWishListed(p)
          )
          this.topProducts.forEach(
            (p: Product) => p.isWishListed = this.wishListService.isProductWishListed(p)
          )
        }
      }
    )

    if(this.bestSellers != null)
      this.bestSellers.forEach(
        p => this.productService.setPreViewValues(p)
      )

    if(this.topProducts != null)
      this.topProducts.forEach(
        p => this.productService.setPreViewValues(p)
      )

    this.components.push(this.bestSellers);
    this.components.push(this.topProducts);
  }

  showTabs(tab: number) {
    const carouselTabs = document.getElementsByClassName('best-seller-carousel-item');

    const controlItems = document.getElementsByClassName('heading-control-item');

    if (tab === 1) {
      this.renderer.addClass(carouselTabs[0], 'active');
      this.renderer.removeClass(carouselTabs[1], 'active');

      this.renderer.addClass(controlItems[0], 'active');
      this.renderer.removeClass(controlItems[1], 'active');
    } else if (tab === 2) {
      this.renderer.addClass(carouselTabs[1], 'active');
      this.renderer.removeClass(carouselTabs[0], 'active');

      this.renderer.addClass(controlItems[1], 'active');
      this.renderer.removeClass(controlItems[0], 'active');
    }

  }

  ngAfterViewInit() {
    const columnDivs = document.getElementsByClassName('best-seller-column-items');

    if (this.bestSellers.length < 4) {
      for (let i = 0; i < this.bestSellers.length; i++) {
        this.renderer.removeClass(columnDivs[i], 'col-md-3');
        this.renderer.addClass(columnDivs[i], 'col-md-' + (12 / this.bestSellers.length));
      }
    }

    if (this.topProducts.length < 4) {
      for (let i = this.bestSellers.length; i < (this.bestSellers.length + this.topProducts.length) ; i++) {
        this.renderer.removeClass(columnDivs[i], 'col-md-3');
        this.renderer.addClass(columnDivs[i], 'col-md-' + (12 / this.topProducts.length));
      }
    }

  }
  
  showModal(product: Product) {
    if(product.inStockQuantity > 0) {
      this.productItem = product;
      this.showProduct = true;
      this.basicModal.show();
    }
  }

  addAndRedirectToKart(product: Product) {
    this.productKartService.addToKart(product.productID, 1, 'new')
    this.productKartService.kartCountLoadedObserver.subscribe(
      (kartCountLoaded) => {
        if(kartCountLoaded)
          this.router.navigate(['ui-kart'])
      }
    )
  }

  addItemToWishList(product: Product) {
    this.wishListService.updateServerWishList(product)
    product.isWishListed = true
  }


}
