import { Injectable } from '@angular/core';
import { UILoaderService } from '../ui-loader/ui-loader.service';
import { NotificationService } from '../notfications/notification.service';

@Injectable()
export class ErrorService {

    constructor(
        private uiLoaderService: UILoaderService, 
        private notifService: NotificationService
    ) {}

    logError(errMsg: string, error: any) {
        console.log('error --> ', errMsg, error)
        this.uiLoaderService.loaderErrorObservable.next(true);
        this.notifService.pushNotification(this.notifService.NOTIF_DANGER, errMsg)
    }

}