import { Component, OnInit, Renderer2, AfterViewInit, Input, ElementRef } from '@angular/core';
import { NotificationService } from '../Utilities/services/notfications/notification.service';

@Component({
  selector: 'app-notification-block',
  templateUrl: './notification-block.component.html',
  styleUrls: ['./notification-block.component.css']
})
export class NotificationBlockComponent implements OnInit, AfterViewInit {

  constructor(public notifService: NotificationService, private renderer: Renderer2) { }

  @Input() notifId: string = '';
  @Input() notifType: string = '';
  @Input() notifMsg: string = '';

  notifEle: HTMLElement;

  ngOnInit(): void {

    if(this.notifId == null || this.notifId.length === 0)
      this.notifId = '0';

    if(this.notifType == null || this.notifType.length === 0)
      this.notifType = this.notifService.NOTIF_INFO;

    console.log('Notification Type ==> ', this.notifType)

    this.notifService.removeNotificationObservable.subscribe(
      (notifId: string) => {
        var notifEle = document.getElementById(this.getId(notifId));
        if(notifEle != null)
          this.renderer.setStyle(notifEle, 'opacity', '0.1');
      }
    )

    this.notifService.notificationReArrangeObservable.subscribe(
      (check) => {
        if(this.notifEle != null)
          this.renderer.setStyle(this.notifEle, 'top', this.getTopPx());
      }
    )

  }

  ngAfterViewInit() {
    this.notifEle = document.getElementById(this.getId(this.notifId));
    this.renderer.setStyle(this.notifEle, 'opacity', '0.7');
    this.renderer.setStyle(this.notifEle, 'top', this.getTopPx())
  }

  getId(id: string) {
    return 'notificationComponent_' + id;
  }

  getTopPx() {
    var len = (+this.notifId * 60) + 100;
    return len + 'px';
  }

  checkNotifType() {
    this.notifType === this.notifService.NOTIF_WARNING
  }

}
