import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable()
export class UILoaderService {

    // Increment here when you add a new loader screen for effective use
    totalLoaders = 3;

    apiLoadingScreen = 'API-LOADER-SCREEN'
    routeLoadingScreen = 'ROUTE-LOADER-SCREEN'
    loginLoaderScreen = 'LOGIN-LOADER-SCREEN'

    /**
     * [loadername, boolean]
     * Pick loader name from above screen variables
     * Send true or false to stop or start the loader screen
     */
    loaderObservable = new Subject<any[]>();

    loaderErrorObservable = new Subject<boolean>();

    constructor() {}

}