import { Injectable } from '@angular/core';
import { LoginService } from './login.service';
import { ProductKartService } from '../products/orders/product-kart.service';
import { HttpClient } from '@angular/common/http';
import { ErrorService } from '../error/error-handler.service';
import { NotificationService } from '../notfications/notification.service';
import { UserService } from './user.service';

@Injectable()
export class LogoutService {

    constructor(        
        private http: HttpClient,
        private errorService: ErrorService,
        private notifService: NotificationService,
        private userService: UserService,
        private kartService: ProductKartService
    ) {}
    
    logoutUser() {
        this.http.get(this.userService.BASE_CUSTOMER_API + 'logout').subscribe(
            (custId: number) => {
                this.logoutHandler()
            },
            error => {
                this.logoutHandler()
                this.errorService.logError('Error while logout', error)
            }
        )
    }

    logoutHandler() {
        let msg = 'You\'re logged out successfully !!!'
        console.log(msg)
        localStorage.removeItem('token');
        this.informAllServices()
        this.notifService.pushNotification(this.notifService.NOTIF_INFO, msg)
    }

    informAllServices() {
        this.kartService.logoutHandler()
        this.userService.logoutHandler()
    }

} 