import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { UserService } from './user.service';
import { ErrorService } from '../error/error-handler.service';
import { Customer } from '../../models/users/customer.model';

@Injectable()
export class LoginService {

  LOGIN_API = 'login';

  constructor(private http: HttpClient, 
    private userService: UserService,
    private errorService: ErrorService) { }

  authTokenLogin(username: string, password: string) {

    this.http.post(this.LOGIN_API, {'userName': username, 'password': password}, {responseType: 'text'}).subscribe(
      (response: string) => {
        console.log('user loggedin his token - ', response)
        localStorage.setItem('token', response);
        this.userService.fetchLogginCustomer();
      },
      error => {
        this.errorService.logError('Please enter valid credentials', error);
      }
    )
  }

  sessionBasedlogin(username: string, password: string) {
    var formData = new FormData()
    formData.append('password', password)
    formData.append('username', username)

    const headers = new HttpHeaders();
    headers.append('Content-Type', 'multipart/form-data');
    headers.append('Accept', 'application/json');

    this.http.post(this.LOGIN_API, formData, {headers: headers}).subscribe(
      (customer: Customer) => {
        console.log('user loggedin - ', customer)
        this.userService.setLoggedInCustomer(customer, 'login')
      },
      error => {
        this.errorService.logError('error occured while loggin in ', error);
      }
    )
  }

}
