import { Component, OnInit } from '@angular/core';
import { UserService } from '../Utilities/services/users/user.service';
import { Customer } from '../Utilities/models/users/customer.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-account-block',
  templateUrl: './account-block.component.html',
  styleUrls: ['./account-block.component.css']
})
export class AccountBlockComponent implements OnInit {

  constructor(
    private userService: UserService,
    private router: Router
  ) { }

  customerLoggedIn: boolean = false;
  customer: Customer

  ngOnInit(): void {

    if(this.customer == null) {
      this.customer = new Customer(0, '', '', '', '', '')
    }

    if(this.userService.customerLoggedIn) {
      this.customer = this.userService.getCustomerDetails()
      this.customerLoggedIn = true;
    }
    
    this.userService.customerLoggedInObserver.subscribe(
      (userLoggedIn) => {
        if(userLoggedIn) {
          this.customer = this.userService.getCustomerDetails()
        } else {
          this.router.navigate(['/', 'home'])
        }
        this.customerLoggedIn = userLoggedIn
      }
    )
  }

}
