import { Component, OnInit, Input } from '@angular/core';
import { ProductKart } from 'src/app/Utilities/models/products/orders/product-kart.model';
import { ProductKartService } from 'src/app/Utilities/services/products/orders/product-kart.service';

@Component({
  selector: 'app-summary-block',
  templateUrl: './summary-block.component.html',
  styleUrls: ['./summary-block.component.css']
})
export class SummaryBlockComponent implements OnInit {

  constructor(
    private kartService: ProductKartService
  ) { }

  kartProducts: ProductKart[] = []

  @Input() totalCheckoutAmount: number = 0

  ngOnInit(): void {

    this.kartService.kartCountLoadedObserver.subscribe(
      (kartCount: number) => {
        if(kartCount > 0)
          this.kartProducts = this.kartService.getKartProducts()
        else
          this.kartProducts = []
      }
    )

    this.kartService.fetchUserKart();

  }

}
