import { Component, OnInit, Input } from '@angular/core';
import { Category } from 'src/app/Utilities/models/global-models/category.model';

@Component({
  selector: 'app-popular-collection-block',
  templateUrl: './popular-collection-block.component.html',
  styleUrls: ['./popular-collection-block.component.css']
})
export class PopularCollectionBlockComponent implements OnInit {

  @Input() popularCategories: Category[] = [];

  constructor(
    
  ) { }

  ngOnInit(): void {
    let count = 1;
    
    if(this.popularCategories != null)
      this.popularCategories.forEach(
        (pc) => {
          pc.viewName = 'Popular ' + pc.categoryName.substring(0, 1).toUpperCase() + pc.categoryName.substring(1).toLowerCase()
        }
      )
  }

}
