import { Component, OnInit, ViewChild } from '@angular/core';
import { Product } from '../Utilities/models/products/product.model';
import { WishListService } from '../Utilities/services/products/orders/wish-list.service';
import { WishList } from '../Utilities/models/products/orders/wish-list.model';
import { ModalDirective } from 'angular-bootstrap-md';

@Component({
  selector: 'app-wishlist-block',
  templateUrl: './wishlist-block.component.html',
  styleUrls: ['./wishlist-block.component.css']
})
export class WishlistBlockComponent implements OnInit {

  constructor(
    private wishListService: WishListService
  ) { }

  isUserLoggedIn = false

  wishList: WishList;
  wishListLoaded = false;

  disableBtn = true

  productItem: Product;
  showProduct: boolean = false;
  @ViewChild('basicModal') basicModal: ModalDirective;

  ngOnInit(): void {
    
    this.wishListService.wishListLoadedObservable.subscribe(
      (wishListLoadedLocal) => {
        if(wishListLoadedLocal)
          this.wishList = this.wishListService.getWishList()

        this.disableBtn = !wishListLoadedLocal
        this.wishListLoaded = wishListLoadedLocal
      }
    )

    this.wishListService.fetchUserWishList()
  }

  openProductModel(product: Product) {
    this.productItem = product
    this.showProduct = true
    this.basicModal.show()
  }

  deleteItemByID(productId: number) {
    if(this.wishList != null && this.wishList.wishListProducts != null && this.wishList.wishListProducts.length > 1)
      this.wishListService.deleteProductFromWishList(productId)
    else if(this.wishList != null && this.wishList.wishListProducts != null && this.wishList.wishListProducts.length == 1)
      this.deleteAllItems()
  }

  deleteAllItems() {
    if(this.wishList != null && this.wishList.wishListProducts != null && this.wishList.wishListProducts.length > 0) 
      this.wishListService.deleteAllItems()
  }

}
