import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { ErrorService } from '../error/error-handler.service';
import { NotificationService } from '../notfications/notification.service';
import { Customer } from '../../models/users/customer.model';
import { CustomerRegister } from '../../models/users/customer-register.model';
import { ProductKartService } from '../products/orders/product-kart.service';
import { finalize } from 'rxjs/operators';
import { WishListService } from '../products/orders/wish-list.service';

@Injectable()
export class UserService {

    BASE_CUSTOMER_API = 'customers/';

    constructor(
        private http: HttpClient,
        private errorService: ErrorService,
        private notifService: NotificationService,
        private kartService: ProductKartService,
        private wishListService: WishListService
    ) {}

    customer: Customer;

    customerLoggedInObserver = new Subject<boolean>();
    customerRegisteredObserver = new Subject<boolean>();

    customerLoggedIn = false;

    
    registerUser(customerRegister: CustomerRegister) {
        this.http.post(this.BASE_CUSTOMER_API + "registerCustomer", customerRegister).subscribe(
            (customer: Customer) => {
                console.log('Registered ===> ', this.customer)
                this.customerRegisteredObserver.next(true)
                this.notifService.pushNotification(
                    this.notifService.NOTIF_SUCCESS, 'User create successfully'
                )
            },
            error => {
                this.customerRegisteredObserver.next(false)
                this.errorService.logError('error occured loading customer ', error);
            }
        )
    }

    fetchLogginCustomer() {
        this.http.get(this.BASE_CUSTOMER_API + 'login').pipe(
            finalize(
               () => {
                   console.log('I\'ll be fired always')
                    this.kartService.fetchUserKart();
               }
            )
        ).subscribe(
            (localCustomer: Customer) => {
                console.log('customer Object received - ', localCustomer)
                this.setLoggedInCustomer(localCustomer, 'login')
            },
            error => {
                console.log('error occured loading customer ', error)
                this.errorService.logError('error occured loading customer ', error);
            }
        )
    }

    checkCustomerLogin() {
        console.log("checking if customer already logged in");

        this.http.get(this.BASE_CUSTOMER_API + 'login/reload')
        .pipe(
            finalize(
               () => {
                   console.log('I\'ll be fired always')
                    this.kartService.fetchUserKart();
               }
            )
        )
        .subscribe(
            (localCustomer: Customer) => {
                console.log('customer Object received - ', localCustomer)
                this.setLoggedInCustomer(localCustomer, 'reload')
            },
            error => {
                console.log('error occured loading customer ', error)
                // this.errorService.logError('error occured loading customer ', error);
            }
        )
    }

    setLoggedInCustomer(localCustomer: Customer, type: string) {
        this.customer = localCustomer;
        this.customerLoggedIn = true;
        this.customerLoggedInObserver.next(this.customerLoggedIn);
        let userName = this.customer.customerLastName + ' ' + this.customer.customerFirstName
        this.pushLoginNotification(userName, type)
        this.wishListService.fetchUserWishList();
    }

    pushLoginNotification(user: string, type: string) {
        let msg = ''
        let notif = ''
        if(user !== null && type === 'login') {
            msg = 'Welcome ' + user + ', you\'re logged in !!!'
            notif = this.notifService.NOTIF_SUCCESS
        }
        else if (user !== null && type === 'reload') {
            msg = 'Welcome back ' + user + ' !!!'
            notif = this.notifService.NOTIF_INFO
        }
        else {
            msg = 'Welcome User !!!'
        }
        
        this.notifService.pushNotification(notif, msg)
    }

    getCustomerDetails() : Customer {
        let localCust = JSON.parse(JSON.stringify(this.customer));
        return localCust;
    }

    changeUserDetails(customer: Customer) {
        this.http.put(this.BASE_CUSTOMER_API + 'updateCustomer', customer).subscribe(
            (localCustomer: Customer) => {
                this.customer = localCustomer
                this.notifService.pushNotification(
                    this.notifService.NOTIF_SUCCESS, 'User details updated successfully'
                )
                this.customerLoggedInObserver.next(true)
            },
            error => {
                this.errorService.logError('error occured updating customer ', error);
            }
        )
    }

    changeUserPassword(customerID: number, oldPassword: string, newPassword: string) {
        let passModel = {customerID: customerID, oldPassword: oldPassword, newPassword: newPassword, status: ''}
        console.log('Password update request ===> ', passModel)

        this.http.put(this.BASE_CUSTOMER_API + 'updateUserPersonal', passModel).subscribe(
            (response) => {
                this.pushPasswordNotification(response['status'])
            },
            error => {
                this.errorService.logError('error occured updating password ', error);
            }
        )
    }
    
    pushPasswordNotification(message: string) {
        let type = ''
        if(message.toLowerCase().includes('failed') || message.toLowerCase().includes('incorrect'))
            type = this.notifService.NOTIF_WARNING
        else
            type = this.notifService.NOTIF_SUCCESS
        
        this.notifService.pushNotification(type, message)
    }

    logoutHandler() {
        this.customerLoggedIn = false;
        this.customerLoggedInObserver.next(false)
    }

}