import { Injectable } from '@angular/core';
import { WishList } from 'src/app/Utilities/models/products/orders/wish-list.model';
import { HttpClient } from '@angular/common/http';
import { ErrorService } from '../../error/error-handler.service';
import { Subject } from 'rxjs';
import { Product } from 'src/app/Utilities/models/products/product.model';
import { ProductService } from '../product.service';
import { ProductUtilService } from '../product-util.service';
import { NotificationService } from '../../notfications/notification.service';

@Injectable()
export class WishListService {

    WISHLIST_API = 'wishlist/'

    constructor(
        private http: HttpClient,
        private productUtilService: ProductUtilService,
        private errorService: ErrorService,
        private notifService: NotificationService
    ){}

    wishList: WishList;
    wishListLoaded = false
    wishListLoadedObservable = new Subject<boolean>()

    fetchUserWishList() {
        this.http.get(this.WISHLIST_API + 'getByLoggedUser').subscribe(
            (localWishList: WishList) => {
                this.successResponse(localWishList, false, '')
            },
            error => {
                this.errorResponse(error)
            }
        )
    }

    getWishList() {
        return this.wishList
    }

    updateServerWishList(product: Product) {
        if(!product.isWishListed) {
            this.http.post(this.WISHLIST_API + 'postWishListItem/' + product.productID, '').subscribe(
                (localWishList: WishList) => {
                    this.successResponse(localWishList, true, 'Item successfully added to wishlist')
                },
                error => {
                    this.errorResponse(error)
                }
            )
        } else {
            this.notifService.pushNotification(this.notifService.NOTIF_INFO, 'Item already in wishlist')
        }

    }

    isProductWishListed(product: Product): boolean {
        if(this.wishList != null && this.wishList.wishListProducts.length > 0) {
            for(let i = 0; i < this.wishList.wishListProducts.length; i++) {
                if(this.wishList.wishListProducts[i].productID === product.productID) {
                    return true;
                }
            }
        }
        return false;
    }

    deleteProductFromWishList(productId: number) {
      this.http.delete(this.WISHLIST_API + 'deleteWishListItem/' + productId).subscribe(
        (localWishList: WishList) => {
            this.successResponse(localWishList, true, 'Item successfully deleted from wishlist')
        },
        error => {
            this.errorResponse(error)
        }
      )
    }

    deleteAllItems() {
      this.http.delete(this.WISHLIST_API + 'deleteWishList').subscribe(
          (wishListDeleted: boolean) => {
              this.wishListLoaded = false
              this.wishListLoadedObservable.next(false)
              this.notifService.pushNotification(this.notifService.NOTIF_WARNING, 'Wishlist removed !!!')
          },
          error => {
              this.errorResponse(error)
          }
      )
    }
    
    errorResponse(error) {
        this.wishListLoaded = false
        this.wishListLoadedObservable.next(this.wishListLoaded)
        this.errorService.logError('Error while loading wishlist ', error)
    }

    successResponse(localWishList: WishList, check: boolean, message: string) {
        if(localWishList !== null && localWishList.wishListProducts !== null && localWishList.wishListProducts.length > 0 ) {
            this.wishList = localWishList
            console.log("WishList items ---> ", localWishList);
            
            this.wishList.wishListProducts.forEach(
                p => { 
                    this.productUtilService.setPreViewValues(p)
                    p.isWishListed = true
                }
            )

            if(check)
                this.notifService.pushNotification(this.notifService.NOTIF_SUCCESS, message)
            this.wishListLoaded = true
            this.wishListLoadedObservable.next(this.wishListLoaded)
        } else {
            if(check)
                this.notifService.pushNotification(this.notifService.NOTIF_WARNING, 'Operation failed on wishlist')
            this.wishListLoaded = false
            this.wishListLoadedObservable.next(this.wishListLoaded)
        }
    }

}