import { Component, OnInit } from '@angular/core';
import { Category } from '../Utilities/models/global-models/category.model';
import { SubCategory } from '../Utilities/models/global-models/sub-category.model';
import { LandingPageService } from '../Utilities/services/landing-page/landing-page.service';
import { Collection } from '../Utilities/models/global-models/collection.model';
import { HeaderService } from '../Utilities/services/landing-page/header.service';

@Component({
  selector: 'app-headers',
  templateUrl: './headers.component.html',
  styleUrls: ['./headers.component.css']
})
export class HeadersComponent implements OnInit {

  constructor(
    private headerService: HeaderService
  ) { }

  womens = new Map<Category, SubCategory[]>();
  mens = new Map<Category, SubCategory[]>();
  kids = new Map<Category, SubCategory[]>();
  
  collections: Collection[]

  womensLoaded:boolean = false;
  mensLoaded:boolean = false;
  kidsLoaded:boolean = false;
  collectionsLoaded:boolean = false;

  navBarLoaded = false;


  ngOnInit(): void {
    this.headerService.headersLoaded.subscribe (
      (localLoaded) => {
        if(localLoaded) {
          this.womens = this.headerService.getWomensHeaders()

          if(this.womens != null && this.womens.size > 0 )
            this.womensLoaded = true
          
          this.mens = this.headerService.getMensHeaders()

          if(this.mens != null && this.mens.size > 0 )
            this.mensLoaded = true
          
          this.kids = this.headerService.getKidsHeaders()

          if(this.kids != null && this.kids.size > 0 )
            this.kidsLoaded = true
          
          this.collections = this.headerService.getCollectionsHeaders()

          if(this.collections != null && this.collections.length > 0 )
            this.collectionsLoaded = true
        }

        this.navBarLoaded = localLoaded
        
      }
    )
  }

}
