import { Component, OnInit, Input } from '@angular/core';
import { CarouselImage } from 'src/app/Utilities/models/landing-page/carousel_image.model';

@Component({
  selector: 'app-carousel-block',
  templateUrl: './carousel-block.component.html',
  styleUrls: ['./carousel-block.component.css']
})
export class CarouselBlockComponent implements OnInit {

  @Input() carouselImages: CarouselImage[];

  constructor() { }

  ngOnInit(): void {
    let count = 1;
    if(this.carouselImages != null)
      this.carouselImages.forEach(
        c => c.imageUrl = 'assets/carousel_' + (count++) + '.jpg'
      )
  }
}
