import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { ProductsCombo } from 'src/app/Utilities/models/landing-page/products-combo.model';
import { Product } from 'src/app/Utilities/models/products/product.model';
import { ModalDirective } from 'angular-bootstrap-md';
import { ProductService } from 'src/app/Utilities/services/products/product.service';
import { ProductKartService } from 'src/app/Utilities/services/products/orders/product-kart.service';
import { Router } from '@angular/router';
import { WishListService } from 'src/app/Utilities/services/products/orders/wish-list.service';
import { UserService } from 'src/app/Utilities/services/users/user.service';

@Component({
  selector: 'app-new-arrivals-block',
  templateUrl: './new-arrivals-block.component.html',
  styleUrls: ['./new-arrivals-block.component.css']
})
export class NewArrivalsBlockComponent implements OnInit {

  @Input() newArrivals: Product[] = [];

  @Input() newArrivalsCombo: ProductsCombo[] = [];

  allProducts: Product[] = [];

  productItem: Product;
  showProduct: boolean = false;

  @ViewChild('basicModal') basicModal: ModalDirective;

  isUserLoggedIn = false;
  
  constructor(
    private productService: ProductService,
    private productKartService: ProductKartService,
    private wishListService: WishListService,
    private userService: UserService,
    private router: Router
  ) { }

  ngOnInit(): void {

    this.isUserLoggedIn = this.userService.customerLoggedIn

    this.userService.customerLoggedInObserver.subscribe(
      (localCustomerLoggedIn) => this.isUserLoggedIn = localCustomerLoggedIn
    )

    this.wishListService.wishListLoadedObservable.subscribe(
      (wishListLoaded) => {
        if(wishListLoaded) {
          this.newArrivals.forEach(
            (p: Product) => p.isWishListed = this.wishListService.isProductWishListed(p)
          )
        }
      }
    )
    
    if(this.newArrivals != null) {
      this.newArrivals.forEach(
        p => this.productService.setPreViewValues(p)
      )
    }

  }

  addAndRedirectToKart(product: Product) {
    this.productKartService.addToKart(product.productID, 1, 'new')
    this.productKartService.kartCountLoadedObserver.subscribe(
      (kartCountLoaded) => {
        if(kartCountLoaded)
          this.router.navigate(['ui-kart'])
      }
    )
  }
  
  showModal(product: Product) {
    if(product.inStockQuantity > 0) {
      this.productItem = product;
      this.showProduct = true;
      this.basicModal.show();
    }
  }

  addItemToWishList(product: Product) {
    this.wishListService.updateServerWishList(product)
    product.isWishListed = true
  }

}
