import { Component, OnInit, Renderer2, Output, EventEmitter } from '@angular/core';
import { Address } from 'src/app/Utilities/models/users/address.model';
import { AddressService } from 'src/app/Utilities/services/users/address.service';
import { ValidationService } from 'src/app/Utilities/services/validation/validation.service';
import { UserService } from 'src/app/Utilities/services/users/user.service';

@Component({
  selector: 'app-user-address-block',
  templateUrl: './user-address-block.component.html',
  styleUrls: ['./user-address-block.component.css']
})
export class UserAddressBlockComponent implements OnInit {

  constructor(
    private renderer: Renderer2,
    private addressService: AddressService,
    private userService: UserService,
    private validationService: ValidationService
  ) { }

  fieldMax = 255
  fieldMin = 5

  @Output() addressIDEmitter = new EventEmitter<number>();
  @Output() paymentReadyEmitter = new EventEmitter<boolean>();

  legend = 'Click to add new address..!!!'

  editAddress: Address;
  errorMsg = ''
  errorField = ''

  userAddresses: Address[] = []
  checkedAddrID = 0

  addrFormOpen = false;

  ngOnInit(): void {
    let customer = this.userService.getCustomerDetails()
    this.editAddress = new Address(0, customer.customerID, '', '', '', '', '', '', false, false, '')

    this.addressService.addressLoadedObserver.subscribe(
      (localAddressesLoaded) => {
        if(localAddressesLoaded)
          this.userAddresses = this.addressService.getUserAddresses()
          
        if(this.userAddresses != null && this.userAddresses.length > 0) {
          this.emitNewAddressID(this.userAddresses[0], 0)
        }
      }
    )

    this.addressService.fetchUserAddress()

  }

  
  openCloseNewAddressForm() {
    let addrFrom = document.getElementById('entire-form-id')
    let addrFieldSet = document.getElementById('field-set-1')

    if(!this.addrFormOpen) {
      this.renderer.addClass(addrFrom, 'entire-form-open')
      this.renderer.addClass(addrFieldSet, 'field-set-border')
    }
    else {
      this.renderer.removeClass(addrFrom, 'entire-form-open')
      this.renderer.removeClass(addrFieldSet, 'field-set-border')
    }

    this.addrFormOpen = !this.addrFormOpen
  }

  emitNewAddressID(addr: Address, index: number) {
    this.checkedAddrID = index
    this.addressIDEmitter.emit(addr.addressID)
    this.paymentReadyEmitter.emit(true)
  }

  addNewAddress() {
    let check = this.validateAddressFields()
    console.log('User address check ==> ', check)
    if(check) {
      this.addressService.saveAddress(this.editAddress);
      this.openCloseNewAddressForm()
      this.legend = 'Select the new address above'
    }
  }
  
  validateAddressFields() {
    this.errorMsg = 'Please follow input field policy'
    if(!this.validationService.validateAddress(this.editAddress.apartmentNumber, this.fieldMax, this.fieldMin)) {
      this.errorField = 'APN'
      return false;
    }

    if(!this.validationService.validateAddress(this.editAddress.street, this.fieldMax, this.fieldMin)) {
      this.errorField = 'STR'
      return false;
    }

    if(!this.validationService.validateAddress(this.editAddress.city, this.fieldMax, this.fieldMin)) {
      this.errorField = 'CTY'
      return false;
    }

    if(!this.validationService.validateAddress(this.editAddress.state, this.fieldMax, this.fieldMin)) {
      this.errorField = 'STA'
      return false;
    }

    if(!this.validationService.validateAddress(this.editAddress.country, this.fieldMax, this.fieldMin)) {
      this.errorField = 'CTR'
      return false;
    }
    
    if(!this.validationService.validateAddress(this.editAddress.zipCode, this.fieldMax, this.fieldMin)) {
      this.errorField = 'ZIP'
      return false;
    }

    this.errorField = ''
    this.errorMsg = ''
    return true;
  }


}
