import { Component, OnInit, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { ValidationService } from 'src/app/Utilities/services/validation/validation.service';
import { CollapseComponent } from 'angular-bootstrap-md';
import { AddressService } from 'src/app/Utilities/services/users/address.service';
import { Address } from 'src/app/Utilities/models/users/address.model';
import { UserService } from 'src/app/Utilities/services/users/user.service';
import { Customer } from 'src/app/Utilities/models/users/customer.model';

@Component({
  selector: 'app-edit-address',
  templateUrl: './edit-address.component.html',
  styleUrls: ['./edit-address.component.css']
})
export class EditAddressComponent implements OnInit {

  @ViewChild('addressForm') addressFrom: CollapseComponent;

  fieldMax = 255
  fieldMin = 5

  showingCollapse = -1;
  userViewAddresses: Address[] = [];
  userEditAddresses: Address[] = [];
  addressLoaded = false;

  editAddress: Address;
  customerLoggedIn: boolean = false;
  customerID: number = 0

  errorMsg = ''
  errorField = ''

  constructor(
    private validationService: ValidationService,
    private addressService: AddressService,
    private userService: UserService
  ) { }

  ngOnInit(): void {
    if(this.userService.customerLoggedIn) {
      this.customerLoggedIn = true
      this.customerID = this.userService.getCustomerDetails().customerID
      this.editAddress = new Address(0 , this.customerID, ' ', ' ', ' ', ' ', ' ', ' ', false, false, ' ');
    }

    this.userService.customerLoggedInObserver.subscribe(
      (userLoggedIn) => {
        if(userLoggedIn) {
          this.addressService.fetchUserAddress();
          this.customerID = this.userService.getCustomerDetails().customerID
          this.editAddress = new Address(0 , this.customerID, ' ', ' ', ' ', ' ', ' ', ' ', false, false, ' ');
        }
        this.customerLoggedIn = userLoggedIn
      }
    )

    this.addressService.addressLoadedObserver.subscribe(
      (addressLoaded: boolean) => {
        if(addressLoaded)
          this.userViewAddresses = this.addressService.getUserAddresses()
          this.userEditAddresses = this.addressService.getUserAddresses()
        
        if(this.userViewAddresses == null || this.userEditAddresses == null) {
          this.userViewAddresses = []
          this.userEditAddresses = []
        }
        this.addressLoaded = addressLoaded;
      }
    )

    this.addressService.fetchUserAddress();

  }

  setAndEditAddress(id: number) {
    console.log('Address edit --> ', id, this.showingCollapse)
    this.errorField = ''
    this.errorMsg = ''

    if(id == this.showingCollapse) {
      this.addressFrom.hide()
      this.showingCollapse = -1
    } else if(id == 0) {
      this.editAddress = new Address(0 , this.customerID, ' ', ' ', ' ', ' ', ' ', ' ', false, false, ' ');
      this.addressFrom.show()
      this.showingCollapse = id
    } else if(this.showingCollapse == -1) {
      this.editAddress = this.userEditAddresses[id-1];
      this.addressFrom.show()
      this.showingCollapse = id
    } else {
      this.editAddress = this.userEditAddresses[id-1];
      this.addressFrom.show()
      this.showingCollapse = id
    }
  }

  validateAndUpdate() {
    let check = false

    check = this.validateAddressFields()
    console.log('Address validation ---> ', check, this.editAddress)

    if(check)
      this.addressService.saveAddress(this.editAddress);

  }

  validateAddressFields() {
    this.errorMsg = 'Please follow address policy'
    if(!this.validationService.validateAddress(this.editAddress.apartmentNumber, this.fieldMax, this.fieldMin)) {
      this.errorField = 'APN'
      return false;
    }

    if(!this.validationService.validateAddress(this.editAddress.street, this.fieldMax, this.fieldMin)) {
      this.errorField = 'STR'
      return false;
    }

    if(!this.validationService.validateAddress(this.editAddress.city, this.fieldMax, this.fieldMin)) {
      this.errorField = 'CTY'
      return false;
    }

    if(!this.validationService.validateAddress(this.editAddress.state, this.fieldMax, this.fieldMin)) {
      this.errorField = 'STA'
      return false;
    }

    if(!this.validationService.validateAddress(this.editAddress.country, this.fieldMax, this.fieldMin)) {
      this.errorField = 'CTR'
      return false;
    }
    
    if(!this.validationService.validateAddress(this.editAddress.zipCode, this.fieldMax, this.fieldMin)) {
      this.errorField = 'ZIP'
      return false;
    }

    this.errorField = ''
    this.errorMsg = ''
    return true;
  }

}
