import { Component, OnInit, ViewChild } from '@angular/core';
import { UserService } from '../Utilities/services/users/user.service';
import { ProductKartService } from '../Utilities/services/products/orders/product-kart.service';
import { ProductKart } from '../Utilities/models/products/orders/product-kart.model';
import { UILoaderService } from '../Utilities/services/ui-loader/ui-loader.service';
import { ModalDirective } from 'angular-bootstrap-md';
import { Product } from '../Utilities/models/products/product.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-product-kart',
  templateUrl: './product-kart.component.html',
  styleUrls: ['./product-kart.component.css']
})
export class ProductKartComponent implements OnInit {

  constructor(
    private userService: UserService,
    private kartService: ProductKartService,
    private uiLoaderService: UILoaderService,
    private router: Router
  ) { }

  disableBtn = false;
  minusFade = false;
  plusFade = false;

  kartProducts: ProductKart[] = [];
  kartLoaded = false;

  totalKartAmount = 0;

  isUserLoggedIn = false;

  productItem: Product;

  showProduct: boolean = false;
  
  @ViewChild('basicModal') basicModal: ModalDirective;


  ngOnInit(): void {

    this.isUserLoggedIn = this.userService.customerLoggedIn

    this.userService.customerLoggedInObserver.subscribe(
      (localUserloggedIn) => {
        this.isUserLoggedIn = localUserloggedIn

        if(this.isUserLoggedIn) {
          this.kartService.fetchUserKart()
        } else {
          this.router.navigate(['/', 'home'])
        }

      }
    )

    this.kartService.kartCountLoadedObserver.subscribe(
      (kartCount: number) => {
        if(kartCount > 0) {
          this.disableBtn = false
          this.kartProducts = this.kartService.getKartProducts()
        
          this.totalKartAmount = this.kartService.getTotalKartAmount()
        } else {
          this.totalKartAmount = 0
          this.disableBtn = true
        }

        this.kartLoaded = kartCount > 0

        if(this.kartProducts == null || this.kartProducts.length == 0) {
          this.kartLoaded = false;
          this.disableBtn = true
        }

        // Stops UI Loader for Kart Items
        this.uiLoaderService.loaderObservable.next([this.uiLoaderService.apiLoadingScreen, false]);
      }
    )

    this.kartService.fetchUserKart()
  }

  openProductModel(product: Product) {
    this.productItem = product
    this.showProduct = true
    this.basicModal.show()
  }

  deleteAllKart() {
    if(this.kartProducts != null && this.kartProducts.length > 0) {
      // Starts UI Loader for Kart Items
      this.uiLoaderService.loaderObservable.next([this.uiLoaderService.apiLoadingScreen, true]);
      this.kartService.deleteKartItems(null, 0)
    }
  }

  deleteKartItemByID(productId: string) {
    this.uiLoaderService.loaderObservable.next([this.uiLoaderService.apiLoadingScreen, true]);
    this.kartService.deleteKartItems(productId, 0)
  }

  adjustProductDetails(type: string, index: number) {
    this.minusFade = this.plusFade = false
    if(type === 'add') {
      this.kartProducts[index].product.cartQuantity++;
    } else if(type === 'minus') {
      this.kartProducts[index].product.cartQuantity--;
    }

    if (this.kartProducts[index].product.cartQuantity > this.kartProducts[index].product.inStockQuantity) {
      this.kartProducts[index].product.cartQuantity = this.kartProducts[index].product.inStockQuantity
      this.plusFade = true;
    }

    if (this.kartProducts[index].product.cartQuantity <= 0) {
      this.kartProducts[index].product.cartQuantity = 1
      this.minusFade = true;
    }

    this.kartProducts[index].quantity = this.kartProducts[index].product.cartQuantity
    
    this.adjustKartList()
  }

  adjustKartList() {
    this.totalKartAmount = 0
    this.kartProducts.forEach(
      (kp) => {
          let amount = this.kartService.getTotalAmount(kp.product.discountedPrice, kp.quantity)
          this.totalKartAmount += amount
          kp.product.totalAmount = amount
      }
    )
    this.totalKartAmount = Math.round(this.totalKartAmount * 100) / 100;
  }

  updateKartAndCheckout() {

    this.kartService.updateKartProducts(this.kartProducts).subscribe (
      (localKartProducts: ProductKart[]) => {
          this.router.navigate(['/', 'ui-checkout']).catch(
            error => {
              console.log("error while redirecting to checkout --> ", error)
            }
          )
      },
      error => {
        this.kartService.kartHandleError(error)
      }
    )

  }

}
