import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { GlobalProductDetails } from 'src/app/Utilities/models/products/filter-sort-model/global-product-details.model';
import { ErrorService } from '../error/error-handler.service';
import { Product } from '../../models/products/product.model';
import { ProductUtilService } from './product-util.service';
import { WishListService } from './orders/wish-list.service';

@Injectable()
export class ProductService {

    PRODUCTS_URL = 'products/';

    constructor(
        private http: HttpClient, 
        private errorService: ErrorService,
        private productUtilService: ProductUtilService,
        private wishListService: WishListService
    ) {}

    productsPage = 0;
    productsForPage = 100;

    allProducts: Product[] = [];
    allProductsObserver = new Subject<boolean>();
    sortCurrentProductsObserver = new Subject<string>();

    fetchProductsBasedOnFilter(globalProductDetails: GlobalProductDetails) {
        console.log('request', globalProductDetails)
        this.http.post(this.PRODUCTS_URL + 'getAllProductsByFilters/' + this.productsPage + '/' + this.productsForPage, globalProductDetails).subscribe(
            (response) => {
                console.log('response', response)
                const localProducts = response['results'];
                this.onSuccess(localProducts)
            },
            error => {
                this.onError(error)
            }
        )
    }

    fetchProductsBasedOnKeyword(query: string) {
        console.log('request product query ---> ', query)
        this.http.get(this.PRODUCTS_URL + 'searchProductByKeyWord/' + query).subscribe(
            (localProducts) => {
                console.log('query products ===> ', localProducts)
                this.onSuccess(localProducts)
            },
            error => {
                this.onError(error)
            }
        )
    }

    onSuccess(localProducts) {
        console.log('localProducts', localProducts)
        this.allProducts = localProducts;

        if(this.allProducts == null)
            this.allProducts = []
        // Set product cart quantity as 1
        this.allProducts.forEach(
            p => this.setPreViewValues(p)
        )
        this.allProductsObserver.next(true);
        console.log('final products ===> ', this.allProducts)
    }

    setPreViewValues(p: Product) {
        this.productUtilService.setPreViewValues(p)
        p.isWishListed = this.wishListService.isProductWishListed(p)
    }
    
    getAllProducts() { 

        if(this.allProducts == null)
            this.allProducts = []

        return this.allProducts.slice();
    }

    onError(error) {
        this.allProducts = [];
        this.allProductsObserver.next(false);
        this.errorService.logError('Error occured while pulling filtered products ', error)
    }

}