import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { of } from 'rxjs';
import { concatMap, delay, repeat } from 'rxjs/operators';
import { ModalDirective } from 'angular-bootstrap-md';
import { UserService } from 'src/app/Utilities/services/users/user.service';
import { Product } from 'src/app/Utilities/models/products/product.model';
import { WishListService } from 'src/app/Utilities/services/products/orders/wish-list.service';

@Component({
  selector: 'app-items-block',
  templateUrl: './items-block.component.html',
  styleUrls: ['./items-block.component.css']
})
export class ItemsBlockComponent implements OnInit {

  constructor(
    private userService: UserService,
    private wishListService: WishListService
  ) { }

  userLoggedIn = false;

  @Input() allProducts: Product[] = [];

  productItem: Product;
  showProduct: boolean = false;

  @ViewChild('basicModal') basicModal: ModalDirective;

  ngOnInit(): void {

    this.userLoggedIn = this.userService.customerLoggedIn

    this.userService.customerLoggedInObserver.subscribe(
      (localUserLoggedIn) => this.userLoggedIn = localUserLoggedIn
    )

    this.wishListService.wishListLoadedObservable.subscribe(
      (wishListLoaded) => {
        if(wishListLoaded)
          this.allProducts.forEach(
            p => p.isWishListed = this.wishListService.isProductWishListed(p)
          )
      }
    )

  }

  getDiscountPrice(priceLocal: number, discountLocal: number) {

    let finalAmount = 0;

    if(discountLocal !== 0) {
      finalAmount = priceLocal * (discountLocal / 100)
      finalAmount = priceLocal - finalAmount;
    }

    finalAmount = Math.round(finalAmount * 100) / 100;

    return finalAmount;
  }

  showModal(product: Product) {
    if(product.inStockQuantity > 0) {
      this.productItem = product;
      this.showProduct = true;
      this.basicModal.show();
    }
  }

  addItemToWishList(product: Product) {
    this.wishListService.updateServerWishList(product)
    product.isWishListed = true
  }

}
