import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ErrorService } from '../error/error-handler.service';
import { Collection } from '../../models/global-models/collection.model';
import { Category } from '../../models/global-models/category.model';
import { SubCategory } from '../../models/global-models/sub-category.model';
import { LandingPage } from '../../models/landing-page/landing-page.model';
import { Section } from '../../models/global-models/section.model';
import { Subject } from 'rxjs';

@Injectable()
export class HeaderService {

    collections: Collection[] = []

    womens = new Map<Category, SubCategory[]>();
    mens = new Map<Category, SubCategory[]>();
    kids = new Map<Category, SubCategory[]>();

    
    headersLoaded = new Subject<boolean>();

    constructor(private http: HttpClient, private errorService: ErrorService) {}

    assignHeaders(landingPage: LandingPage) {
        let headers = landingPage.navBar
        console.log('Navbar -->> ', headers)
        
        if(headers == null)
            throw new Error('header object should not be null');
        
        Object.keys(headers).forEach(
            (key) => {
                var section: Section = JSON.parse(key);
                if( section.sectionName.toLowerCase() === 'womens') {
                    this.womens = this.getObjectForMap(headers, key);
                    console.log('Women headers --> ', this.womens)
                }
                else if( section.sectionName.toLowerCase() === 'mens') {
                    this.mens = this.getObjectForMap(headers, key)
                    console.log('Mens headers --> ', this.mens)
                } 
                else  if( section.sectionName.toLowerCase() === 'kids') {
                    this.kids = this.getObjectForMap(headers, key)
                    console.log('Kids headers --> ', this.kids)
                }
            }
        )

        this.collections = landingPage.globalCollectionList

        
        this.headersLoaded.next(true);
    }

    getObjectForMap(headers, key) {
        var localMap = new Map<Category, SubCategory[]>();

        var localHeader = headers[key]

        if(localHeader == null)
            throw new Error('header object should not be null');

        Object.keys(headers[key]).forEach(
            (catKey) => {
                var category: Category = JSON.parse(catKey);
                localMap.set(category, headers[key][catKey])
            }
        )
        return localMap
    }

    getWomensHeaders() {
        return this.womens;
    }

    getMensHeaders() {
        return this.mens;
    }

    getKidsHeaders() {
        return this.kids;
    }

    getCollectionsHeaders() {
        return this.collections.slice();
    }
}