import { Component, OnInit, AfterViewInit, Renderer2 } from '@angular/core';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { UILoaderService } from './Utilities/services/ui-loader/ui-loader.service';
import { Router, RouterEvent, NavigationStart, NavigationEnd, NavigationCancel, NavigationError } from '@angular/router';
import { NotificationService } from './Utilities/services/notfications/notification.service';
import { LandingPageService } from './Utilities/services/landing-page/landing-page.service';
import { UserService } from './Utilities/services/users/user.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  routeUILoaderType = 'three-strings';
  apiUILoaderType = 'square-jelly-box';
  loginUILoaderType = 'wandering-cubes'
  uiLoaderColor = 'rgb(230, 200, 110)'

  showNotification = [];
  notificationType = '';
  notificationMessage = '';

  constructor(
    private router: Router,
    private landingPageService: LandingPageService,
    private ngxService: NgxUiLoaderService,
    public uiLoaderService: UILoaderService, 
    private notifService: NotificationService,
    private renderer: Renderer2,
    private userService: UserService
  ) {
    this.router.events.subscribe((e: RouterEvent) => {
      window.scrollTo(0, 0)
      this.navigationInterceptor(e);
    })
  }

  ngOnInit() {
    this.ngxService.startLoader(this.uiLoaderService.routeLoadingScreen);

    this.setTheObservables();
  }


  setTheObservables() {
    
    // Loads all Global Models
    this.userService.checkCustomerLogin();
    this.landingPageService.fetchAllInfo();

    // Below Observable controls display and stopping of Loader UI
    this.uiLoaderService.loaderObservable.subscribe(
      (loader: string[]) => {
        console.log('Ui Loader status --> ', loader)
        if (loader[1]) {
          window.scrollTo(0, 0)
          // To stop user scroll while UI loader is playing
          this.renderer.setStyle(document.documentElement, 'overflow', 'hidden')
          this.ngxService.startLoader(loader[0]);
        } else {
          this.ngxService.stopLoader(loader[0]);
          // To stop user scroll while UI loader is playing
          setTimeout(() => {
            this.renderer.setStyle(document.documentElement, 'overflow', 'auto')
          }, 1000);
        }
      }
    )

    // Below Observable stops all the Loader UI's
    this.uiLoaderService.loaderErrorObservable.subscribe(
      (disable) => {
        this.ngxService.stopAllLoader(this.uiLoaderService.routeLoadingScreen)
        this.ngxService.stopAllLoader(this.uiLoaderService.apiLoadingScreen)
        this.ngxService.stopAllLoader(this.uiLoaderService.loginLoaderScreen)
        // To stop user scroll while UI loader is playing
        setTimeout(() => {
          this.renderer.setStyle(document.documentElement, 'overflow', 'auto')
        }, 1000);
      }
    )

    //Below Observable generates notification
    this.notifService.notificationObservable.subscribe(
      (notifDetails: string[]) => {
        this.notificationType = notifDetails[1]
        this.notificationMessage = notifDetails[2]
        this.showNotification.push(notifDetails);
        console.log('Elements in notif list --> ', this.showNotification);

        setTimeout(() => {
          this.notifService.removeNotificationObservable.next(notifDetails[0]);
        }, 9300);

        setTimeout(() => {
          this.showNotification.splice(0, 1);
          console.log('removed last item --> ', this.showNotification)
          this.arrangeNotificationItems();
        }, 10000);

      }
    )

  }

  // Not using now...!!!
  checkAnyLoaderRunning(loaderName: string) {
    if (this.ngxService.hasRunningTask(true, this.ngxService.getLoader(loaderName).loaderId))
      return true;

    return false;
  }

  removeNotification(notifId: string) {
    this.notifService.removeNotificationObservable.next(notifId);
    setTimeout(() => {
      const index = this.showNotification.findIndex(x => x[0] === notifId);
      console.log('deleted notification element index --> ', index);
      if (index > -1) {
        console.log('deleted notification element --> ', this.showNotification.splice(index, 1));
        console.log('notification list --> ', this.showNotification);
        this.arrangeNotificationItems();
      }
    }, 500);
  }

  arrangeNotificationItems() {
    var count  = 0;
    this.showNotification.forEach(
      el => {
        el[0] = count++;
      }
    )
    console.log('Element after rearranging ', this.showNotification)
    this.notifService.decrementNotificationCount();

    setTimeout(() => {
      this.notifService.notificationReArrangeObservable.next(true);
    }, 500);
  }

  // Shows and hides the loading spinner during RouterEvent changes
  navigationInterceptor(event: RouterEvent): void {
    if (event instanceof NavigationStart) {
      if (!event.url.startsWith('/ui-products/'))
        this.uiLoaderService.loaderObservable.next([this.uiLoaderService.routeLoadingScreen, true])
    }
    if (event instanceof NavigationEnd) {
      this.uiLoaderService.loaderObservable.next([this.uiLoaderService.routeLoadingScreen, false])
    }

    // Set loading state to false in both of the below events to hide the spinner in case a request fails
    if (event instanceof NavigationCancel) {
      this.uiLoaderService.loaderObservable.next([this.uiLoaderService.routeLoadingScreen, false])
    }
    if (event instanceof NavigationError) {
      this.uiLoaderService.loaderObservable.next([this.uiLoaderService.routeLoadingScreen, false])
    }
  }
}
