import { Component, OnInit } from '@angular/core';
import { LoginService } from 'src/app/Utilities/services/users/login.service';
import { UserService } from 'src/app/Utilities/services/users/user.service';
import { Customer } from 'src/app/Utilities/models/users/customer.model';
import { UILoaderService } from 'src/app/Utilities/services/ui-loader/ui-loader.service';
import { CustomerRegister } from 'src/app/Utilities/models/users/customer-register.model';
import { ValidationService } from 'src/app/Utilities/services/validation/validation.service';
import { LogoutService } from 'src/app/Utilities/services/users/logout.service';

@Component({
  selector: 'app-top-header',
  templateUrl: './top-header.component.html',
  styleUrls: ['./top-header.component.css']
})
export class TopHeaderComponent implements OnInit {

  username: string;
  password: string;
  userLoggedIn: boolean = false;
  userRegistered: boolean = false;

  loggedInCustomer: Customer;

  customerRegister: CustomerRegister
  
  loginErrorMsg = ''
  loginErrField = ''

  registerErrMsg = ''
  registerErrField = ''

  constructor(
    private loginService: LoginService, 
    private userService: UserService,
    private uiLoaderService: UILoaderService,
    private validationService: ValidationService,
    private logoutService: LogoutService
  ) { }

  ngOnInit(): void {
    this.username = 'seethend';
    this.password = 'See12th3';

    this.customerRegister = new CustomerRegister(0, '' ,'', '', '', false, '', '', '', 0);

    this.userService.customerLoggedInObserver.subscribe(
      (customerLoggedIn: boolean) => {
        this.userLoggedIn = customerLoggedIn;
        if(customerLoggedIn)
          this.loggedInCustomer = this.userService.getCustomerDetails();
        
        // Loader UI stopped after login request got response
        console.log(this.uiLoaderService.loginLoaderScreen + ' set to ' + false)
        this.uiLoaderService.loaderObservable.next([this.uiLoaderService.loginLoaderScreen, false]);
      }
    )

    this.userService.customerRegisteredObserver.subscribe(
      (localCustomerRegistered) => {
        if(!localCustomerRegistered)
          this.registerErrMsg = 'User registration Failed, username or email already exists !!!'
        this.userRegistered = localCustomerRegistered
        // this.customerRegister = new CustomerRegister(0, '' ,'', '', '', false, '', '', '', 0);
      }
    )

  }

  login() {
    if (this.username.length !== 0) {
      if (this.password.length !== 0) {
        // Loader UI started for login
        console.log(this.uiLoaderService.loginLoaderScreen + ' set to ' + true)
        this.uiLoaderService.loaderObservable.next([this.uiLoaderService.loginLoaderScreen, true]);
        // this.loginService.authTokenLogin(this.username, this.password);
        this.loginService.sessionBasedlogin(this.username, this.password);

        this.loginErrorMsg = ''
        this.loginErrField = ''
        return true;

      } else {
        this.loginErrorMsg = 'Please enter password'
        this.loginErrField = 'PS'
        return false;
      }
    } else {
      this.loginErrorMsg = 'Please enter username'
      this.loginErrField = 'UN'
      return false;
    }
  }

  register() {
    this.registerErrMsg = 'Please follow the input field policy'
    if(!this.validationService.validateUserName(this.customerRegister.userName, 30, 5)) {
      this.registerErrField = 'UN'
      return false
    }

    if(!this.validationService.validateText(this.customerRegister.customerFirstName, 30, 5)) {
      this.registerErrField = 'FN'
      return false
    }

    if(!this.validationService.validateText(this.customerRegister.customerLastName, 30, 5)) {
      this.registerErrField = 'LN'
      return false
    }

    if(!this.validationService.validateEmail(this.customerRegister.emailAddress, 125, 5)) {
      this.registerErrField = 'EM'
      return false
    }
    
    if(!this.validationService.validatePhone(this.customerRegister.phone, 25, 10)) {
      this.registerErrField = 'PH'
      return false
    }

    if(this.customerRegister.repassword !== this.customerRegister.password) {
      this.registerErrField = 'RP'
      return false
    } else if(!this.validationService.validatePassword(this.customerRegister.password, 30, 8)) {
      this.registerErrField = 'PS'
      return false
    }

    console.log('Customer register ---> ', this.customerRegister)
    this.userService.registerUser(this.customerRegister)

    this.registerErrField = ''
    this.registerErrMsg = ''
    return true;

  }

  registerAgain() {
    this.userRegistered = false;
  }

  logoutUser() {
    this.logoutService.logoutUser()
  }

}
