import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HeaderService } from './header.service';
import { Subject } from 'rxjs';
import { ErrorService } from '../error/error-handler.service';
import { GlobalModelsService } from '../global/global-models.service';
import { LandingPage } from '../../models/landing-page/landing-page.model';

@Injectable()
export class LandingPageService {

    LANDING_URL = '/landing-page'

    constructor(
        private http: HttpClient,
        private headerService: HeaderService,
        private globalService: GlobalModelsService,
        private errorService: ErrorService
    ) {}

    landingPage: LandingPage = null;

    landingPageLoaded = new Subject<boolean>();

    fetchAllInfo() {
        let check = true
        this.http.get(this.LANDING_URL).subscribe(
            (response: LandingPage) => {
                console.log('Landing Page response ===> ', response);
                this.landingPage = response;
                try {
                    this.globalService.assignGlobalModels(this.landingPage)
                    this.headerService.assignHeaders(this.landingPage)
                    this.landingPageLoaded.next(true)
                } catch(err) {
                    this.landingPageLoaded.next(false)
                    this.errorService.logError('Error while assigning global models or headers', err)
                }
            },
            error => {
                this.landingPageLoaded.next(false)
                this.errorService.logError('Error while loading landing page', error)
            }
        )
    }

    getLandingPage() {
        return this.landingPage;
    }

}