import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { ErrorService } from '../../error/error-handler.service';
import { NotificationService } from '../../notfications/notification.service';
import { ChargeRequest } from 'src/app/Utilities/models/products/orders/charge-request.mode';
import { Checkout } from 'src/app/Utilities/models/products/orders/check-out.model';

@Injectable()
export class CheckOutService {
    
    PYMT_API = 'payments/'

    constructor(
        private http: HttpClient,
        private errorService: ErrorService,
        private notifService: NotificationService
    ){}

    checkOut: Checkout;

    checkOutObservable = new Subject<boolean>();
    paymentSuccessObservable = new Subject<boolean>();

    performCheckout() {
        this.http.get(this.PYMT_API + 'checkout').subscribe(
            (checkOut: Checkout) => {
                this.checkOut = checkOut
                this.checkOutObservable.next(true)
            },
            error => {
                this.checkOutObservable.next(false)
                this.errorService.logError('Error while checkout ', error)
            }
        )
    }

    getCheckOutDetails() {
        return this.checkOut
    }
    
    chargeCustomer(chargeRequest: ChargeRequest) {
        console.log('checkout request ===> ', chargeRequest)
        this.http.post(this.PYMT_API + 'charge', chargeRequest).subscribe(
            (response: Response) => {
                console.log('checkout response ==>', response)
                this.paymentSuccessObservable.next(true)
                this.pushPaymentNotification(true, 'Payment successfull !!!');
            },
            error => {
                this.paymentSuccessObservable.next(false)
                this.pushPaymentNotification(false, 'Payment Failed !!!');
                this.errorService.logError('Error while payment ', error)
            }
        )
    }

    pushPaymentNotification(status: boolean, msg: string) {
        this.notifService.pushNotification(
            status ? this.notifService.NOTIF_SUCCESS : this.notifService.NOTIF_WARNING,
            msg
        )
    }
  

}