import { Component, OnInit, Input, Output } from '@angular/core';
import { ModalDirective } from 'angular-bootstrap-md';
import { UserService } from 'src/app/Utilities/services/users/user.service';
import { ProductKartService } from 'src/app/Utilities/services/products/orders/product-kart.service';
import { Product } from 'src/app/Utilities/models/products/product.model';
import { Router } from '@angular/router';
import { WishListService } from 'src/app/Utilities/services/products/orders/wish-list.service';

@Component({
  selector: 'app-product-item',
  templateUrl: './product-item.component.html',
  styleUrls: ['./product-item.component.css']
})
export class ProductItemComponent implements OnInit {

  userloggedIn = false;

  @Input() basicModal: ModalDirective;
  @Input() productItem: Product;
  @Input() allowBuy: boolean

  constructor(
    private userService: UserService,
    private productKartService: ProductKartService,
    private wishListService: WishListService,
    private router: Router
  ) { }

  ngOnInit(): void {

    this.userloggedIn = this.userService.customerLoggedIn

    this.userService.customerLoggedInObserver.subscribe(
      (customerLoggedIn) => {
        this.userloggedIn = customerLoggedIn
      }
    )

    this.wishListService.wishListLoadedObservable.subscribe(
      (wishListLoaded) => {
        if(wishListLoaded) {
          this.productItem.isWishListed = this.wishListService.isProductWishListed(this.productItem)
        }
      }
    )

  }

  closeModal() {
    this.basicModal.hide();
  }

  adjustTotalPrice() {
    this.productItem.totalAmount = Math.round((this.productItem.discountedPrice * this.productItem.cartQuantity) * 100) / 100;
  }

  addToKart() {
    this.productKartService.addToKart(this.productItem.productID, this.productItem.cartQuantity, 'new')
  }

  adjustProductDetails(type: string) {
    if (type === 'add' && this.productItem.cartQuantity < +this.productItem.inStockQuantity)
      this.productItem.cartQuantity++;

    if (type === 'minus' && +this.productItem.cartQuantity > 1)
      this.productItem.cartQuantity--;

    if (this.productItem.cartQuantity > 1 && this.productItem.cartQuantity <= this.productItem.inStockQuantity)
      this.adjustTotalPrice()
  }

  checkExceeded() {
    if (this.productItem.cartQuantity > this.productItem.inStockQuantity)
      this.productItem.cartQuantity = this.productItem.inStockQuantity

    if (this.productItem.cartQuantity <= 0)
      this.productItem.cartQuantity = 1
  }

  addAndRedirectToKart() {
    this.productKartService.addToKart(this.productItem.productID, this.productItem.cartQuantity, 'new')
    this.productKartService.kartCountLoadedObserver.subscribe(
      (kartCountLoaded) => {
        if(kartCountLoaded)
          this.router.navigate(['ui-kart'])
      }
    )
  }

}
