import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ProductKart } from 'src/app/Utilities/models/products/orders/product-kart.model';
import { Subject } from 'rxjs';
import { ErrorService } from '../../error/error-handler.service';
import { NotificationService } from '../../notfications/notification.service';
import { ProductService } from '../product.service';
import { Product } from 'src/app/Utilities/models/products/product.model';

@Injectable()
export class ProductKartService {

    KART_API = 'cart/'

    kartProducts: ProductKart[] = [];
    kartCountLoadedObserver = new Subject<number>();

    totalKartAmount = 0;

    kartCount = 0

    constructor(
        private http: HttpClient,
        private productService: ProductService,
        private errorService: ErrorService,
        private notifService: NotificationService
    ) {}

    fetchUserKart() {
        this.http.get(this.KART_API + 'get-cart/').subscribe(
            (localKartProducts: ProductKart[]) => {
                this.setPreViewForKartProducts(localKartProducts)
            },
            error => {
                this.kartHandleError(error)
            }
        )
    }

    setPreViewForKartProducts(localKartProducts: ProductKart[]) {
        this.totalKartAmount = 0;
        if(localKartProducts != null)
            this.kartProducts = localKartProducts
        else
            this.kartProducts = []

        this.kartProducts.forEach(
            pk => this.setPreViewValues(pk)
        )
        
        this.kartCount = this.kartProducts.length
        console.log('Kart Products ---> ', this.kartProducts)
        this.kartCountLoadedObserver.next(this.kartCount)
    }

    setPreViewValues(pk: ProductKart) {
        // Set the product data first, avoid discount related issues
        this.productService.setPreViewValues(pk.product)

        if(pk.quantity > pk.product.inStockQuantity) {
            pk.quantity = pk.product.inStockQuantity
            pk.product.cartQuantity = pk.product.inStockQuantity
        } else if (pk.quantity < 0) {
            pk.quantity = 1
            pk.product.cartQuantity = 1
        }

        pk.product.cartQuantity = pk.quantity
        let amount = this.getTotalAmount(pk.product.discountedPrice, pk.quantity)
        this.totalKartAmount += amount
        pk.product.totalAmount = amount
    }

    getTotalAmount(discountedPrice: number, quantity: number): number {
        let finalAmount = 0;

        finalAmount = Math.round((discountedPrice * quantity) * 100) / 100;
    
        return finalAmount;
    }

    getKartProducts(): ProductKart[] {
        if(this.kartProducts == null)
            this.kartProducts = []

        return this.kartProducts.slice();
    }

    getTotalKartAmount() {
        console.log('Total kart Amount --> ', this.totalKartAmount)
        return Math.round(this.totalKartAmount * 100) / 100;
    }

    addToKart(productId: string, cartQty: number, type: string) {
        let localProdId = +productId;

        this.http.post(this.KART_API + 'add-to-cart/'+ localProdId + '/' + cartQty, {}).subscribe(
            (localKartProducts: ProductKart[]) => {
                this.setPreViewForKartProducts(localKartProducts)
                this.pushAddNotification('new')
            },
            error => {
                this.kartHandleError(error)
            }
        )
    }

    pushAddNotification(type: string) {
        let msg = ''

        if(type === 'new')
            msg = 'Item Added to the Cart Successfully'
        else if(type === 'old')
            msg = 'Qunatity increased on item Successfully'

        if(msg !== '')
            this.notifService.pushNotification(this.notifService.NOTIF_SUCCESS, msg)
    }

    deleteKartItems(productId: string, quantity: number) {
        let localApi = ''

        if(productId == null && quantity == 0)
            localApi = 'empty-cart/'
        else if(productId != null && quantity == 0)
            localApi = 'delete-cart-item/' + productId
        else if(productId != null && quantity > 0)
            localApi = 'decrease-cart-item/' + productId + "/" + quantity

        this.http.delete(this.KART_API + localApi).subscribe (
            (localKartProducts: ProductKart[]) => {
                this.setPreViewForKartProducts(localKartProducts)
                this.pushDeletNotification(localApi);
            },
            error => {
                this.kartHandleError(error)
            }
        )
    }

    pushDeletNotification(localApi: string) {
        let msg = ''
        if(localApi.startsWith('empty-cart'))
            msg = 'Successfully removed all items from cart...!!'
        else if(localApi.startsWith('delete-cart-item'))
            msg = 'Successfully removed item from cart...!!'
        else if(localApi.startsWith('decrease-cart-item'))
            msg = 'Successfully decreased quantity of cart item...!!'

        if(msg !== '')
            this.notifService.pushNotification(this.notifService.NOTIF_WARNING, msg)
    }

    updateKartProducts(kartProducts: ProductKart[]) {
        return this.http.put(this.KART_API + 'updateCart', kartProducts)
    }

    kartHandleError(error) {
        this.kartProducts = []
        this.kartCountLoadedObserver.next(this.kartCount)
        this.errorService.logError('Error while loading all cart items ', error)
    }

    logoutHandler() {
        this.kartProducts = []
        this.kartCount = 0
        this.totalKartAmount = 0
        this.kartCountLoadedObserver.next(0)
    }

}