import { Injectable } from '@angular/core';
import { ProductFilter } from '../../models/products/filter-sort-model/product-filter.model';
import { ProductSort } from '../../models/products/filter-sort-model/product-sort.model';
import { GlobalProductDetails } from '../../models/products/filter-sort-model/global-product-details.model';
import { Product } from '../../models/products/product.model';
import { ProductService } from './product.service';
import { GlobalModelsService } from '../global/global-models.service';

@Injectable()
export class ProductSearchService {
    
    constructor(
        private productService: ProductService
    ){}

    searchForCriteria(section: string, categoryType: string, catergorySubType: string) {
        
        let globalProductDetails = this.prepareGlobalProductDetails(
            section, 
            categoryType, 
            catergorySubType, 
            'new listed'
        );

        this.productService.fetchProductsBasedOnFilter(globalProductDetails);
    }
    
    searchForKeyword(query: string) {
        this.productService.fetchProductsBasedOnKeyword(query);
    }

    sortProducts(allProducts: Product[], sortCriteria: string): Product[] {
        return allProducts.sort(
            (product1: Product, product2: Product) => {
                if(sortCriteria === 'Newly Listed')
                    return (product1.updateDate > product2.updateDate) ? -1 : 1;
                else if(sortCriteria === 'Price - High to Low')
                    return (product1.price > product2.price) ? -1 : 1;
                else if(sortCriteria === 'Price - Low to High')
                    return (product1.price < product2.price) ? -1 : 1;
                else if('Discount')
                    return (product1.discount > product2.discount) ? -1 : 1;
                
                return 0;
            }
        )
    }

    prepareGlobalProductDetails(section: string, categoryName: string, subCategoryName: string, sortBy: string) {
        var filters = {};

        var priceFilter = [new ProductFilter(0, '', true, 0, 0, 10000)];
        filters['price-filter'] = priceFilter;

        if(categoryName != null && categoryName.length > 0) {
            var categoryFilter = [new ProductFilter(0, categoryName, true, 0, 0, 0)];
            filters['category-filter'] = categoryFilter;
        }

        if(subCategoryName != null && subCategoryName.length > 0) {
            var subCategoryFilter = [new ProductFilter(0, subCategoryName, true, 0, 0, 0)];
            filters['sub-category-filter'] = subCategoryFilter;
        }

        if(section != null && section.length > 0) {
            var sectionFilter = [new ProductFilter(0, section, true, 0, 0, 0)];
            filters['gender'] = sectionFilter;
        }

        var sort = [new ProductSort(0, sortBy.toLowerCase(), true, 0)]
        var globalProduct = new GlobalProductDetails(filters, sort);

        return globalProduct;
    }

    prepareGlobalProductDetailsForFilter(
        selectedFilters: Map<string, string[]>,
        selectedSort: string,
        priceMin: string,
        priceMax: string
    ) {
        var filters = {};

        var priceFilter = [new ProductFilter(0, '', true, 0, +priceMin, +priceMax)];
        filters['price-filter'] = priceFilter;

        selectedFilters.forEach(
            (filterValue: string[], filterKey: string) => {
                switch (filterKey) {
                    case 'Categories':
                        var categoryFilter = []
                        filterValue.forEach(
                            (filter) => {
                                categoryFilter.push(new ProductFilter(0, filter, true, 0, 0, 0))
                            }
                        )
                        filters['category-filter'] = categoryFilter;
                        break;
                    case 'Sub Categories':
                        var subCategoryFilter = []
                        filterValue.forEach(
                            (filter) => {
                                subCategoryFilter.push(new ProductFilter(0, filter, true, 0, 0, 0))
                            }
                        )
                        filters['sub-category-filter'] = subCategoryFilter;
                        break;
                    case 'Materials':
                        var materialFilter = []
                        filterValue.forEach(
                            (filter) => {
                                materialFilter.push(new ProductFilter(0, filter, true, 0, 0, 0))
                            }
                        )
                        filters['materials'] = materialFilter;
                        break;
                    case 'Section':
                        var sectionFilter = []
                        filterValue.forEach(
                            (filter) => {
                                sectionFilter.push(new ProductFilter(0, filter, true, 0, 0, 0))
                            }
                        )
                        filters['gender'] = sectionFilter;
                        break;
                    case 'Metal Purity':
                        var purityFilter = []
                        filterValue.forEach(
                            (filter) => {
                                purityFilter.push(new ProductFilter(0, filter, true, 0, 0, 0))
                            }
                        )
                        filters['materials_purity'] = purityFilter;
                        break;
                    case 'Offers':
                        var offersFilter = []
                        filterValue.forEach(
                            (filter) => {
                                offersFilter.push(new ProductFilter(0, filter, true, 0, 0, 0))
                            }
                        )
                        filters['discount'] = offersFilter;
                        break;
                    default:
                        filters['category-filter'] = [];
                        filters['Materials'] = [];
                        filters['gender'] = [];
                        filters['materials_purity'] = [];
                        filters['offers'] = [];
                        break;
                }
            }
        )

        var sort = []
        switch (selectedSort) {
            case 'Discount':
                sort.push(new ProductSort(0, 'discount', true, 0));
                break;
            case 'Price - High to Low':
                sort.push(new ProductSort(0, 'price high to low', true, 0));
                break;
            case 'Price - Low to High':
                sort.push(new ProductSort(0, 'price low to high', true, 0));
                break;
            case 'Newly Listed':
                sort.push(new ProductSort(0, 'new listed', true, 0));
                break;
            default:
                sort.push(new ProductSort(0, 'new listed', true, 0));
                break;
        }
        var globalProduct = new GlobalProductDetails(filters, sort);

        return globalProduct;
    }
}