import { Component, OnInit } from '@angular/core';
import { UserService } from '../Utilities/services/users/user.service';
import { CheckOutService } from '../Utilities/services/products/orders/check-out.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-check-out',
  templateUrl: './check-out.component.html',
  styleUrls: ['./check-out.component.css']
})
export class CheckOutComponent implements OnInit {

  constructor(
    private userService: UserService,
    private checkOutService: CheckOutService,
    private router: Router
  ) { }

  totalAmount = 0
  stripePublicKey = ''
  currency = ''
  checkOutCompleted = false;

  customerLoggedIn: boolean

  ngOnInit(): void {
    
    this.customerLoggedIn = this.userService.customerLoggedIn

    this.userService.customerLoggedInObserver.subscribe(
      (userLoggedIn: boolean) => {
        this.customerLoggedIn = userLoggedIn

        if(!userLoggedIn) {
          this.router.navigate(['/', 'home'])
        }

      }
    )

    this.checkOutService.checkOutObservable.subscribe(
      (checkOutDone) => {
        if(checkOutDone) {
          let checkOut = this.checkOutService.getCheckOutDetails()
          this.totalAmount = checkOut.amount
          this.stripePublicKey = checkOut.stripePublicKey
          this.currency = checkOut.currency
        }
        this.checkOutCompleted = checkOutDone
      }
    )

    this.checkOutService.performCheckout();


  }

}
