import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeBlocksComponent } from './home-blocks/home-blocks.component';
import { ProductBlocksComponent } from './product-blocks/product-blocks.component';
import { ItemsBlockComponent } from './product-blocks/items-block/items-block.component';
import { ProductKartComponent } from './product-kart/product-kart.component';
import { AccountBlockComponent } from './account-block/account-block.component';
import { EditPersonalDetailsComponent } from './account-block/edit-personal-details/edit-personal-details.component';
import { EditAddressComponent } from './account-block/edit-address/edit-address.component';
import { CheckOutComponent } from './check-out/check-out.component';
import { WishlistBlockComponent } from './wishlist-block/wishlist-block.component';


const routes: Routes = [
  {
    path: '', pathMatch: 'full', redirectTo: 'home'
  },
  {
    path: 'home', component: HomeBlocksComponent
  },
  {
    path: 'ui-products', component: ProductBlocksComponent, children: [
      {
        path: 'search', component: ItemsBlockComponent
      }
    ]
  },
  {
    path: 'ui-kart', component: ProductKartComponent
  },
  {
    path: 'ui-account', component: AccountBlockComponent, children: [
      {
        path: 'edit/address', component: EditAddressComponent
      },
      {
        path: 'edit', component: EditPersonalDetailsComponent
      },
    ]
  },
  {
    path: 'ui-checkout', component: CheckOutComponent
  },
  {
    path: 'ui-wishlist', component: WishlistBlockComponent
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})], //, {useHash: true}
  exports: [RouterModule]
})
export class AppRoutingModule { }
