import { Injectable } from '@angular/core';
import { Product } from '../../models/products/product.model';
import { GlobalModelsService } from '../global/global-models.service';
import { WishListService } from './orders/wish-list.service';

@Injectable()
export class ProductUtilService {

    constructor(
        private globalModalsService: GlobalModelsService
    ){}

    setPreViewValues(p: Product) {
        p.cartQuantity = p.inStockQuantity > 0 ? 1 : 0;
        p.totalAmount = p.price;

        let category = this.globalModalsService.getCategoryByID(p.categoryID)
        p.categoryName = category !== null ? category.categoryName : ''

        let subCategory = this.globalModalsService.getSubCategoryByID(p.subCategoryID)
        p.subCategoryName = subCategory !== null ? subCategory.subCategoryName : ''

        let section = this.globalModalsService.getSectionByID(p.sectionID)
        p.sectionName = section !== null ? section.sectionName : ''

        let collection = this.globalModalsService.getCollectionByID(p.collectionID)
        p.collectionName = collection !== null ? collection.collectionName : ''

        let materials = this.globalModalsService.getMaterialByID(p.materialID)
        p.materialName = materials !== null ? materials.materialType : 'Change it!!'

        let metalPurity = this.globalModalsService.getMetalPurityByID(p.metalPurityID)
        p.metalPurityName = metalPurity !== null ? metalPurity.purity : 'Change it!!'

        let discount = this.globalModalsService.getDiscountByID(p.discountID)
        p.discount = discount !== null ? discount.percentage : 0

        p.discountedPrice = this.getDiscountPrice(p.price, p.discount)

    }

    getDiscountPrice(price: number, discount: number): number {
        let discountedPrice = price

        if (discount !== 0) {
            discountedPrice = price * (discount / 100)
            discountedPrice = price - discountedPrice;
        }
        
        discountedPrice = Math.round(discountedPrice * 100) / 100;

        return discountedPrice;
    }

}