import { Injectable } from '@angular/core';

@Injectable()
export class ValidationService {

    TEXT_REGEX = /^[a-z\sA-Z]+$/
    USER_REGEX = /^[a-zA-Z0-9.!#$%&;*+/=?^_`{|}~-]+$/
    EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&;*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
    PASS_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/
    NUM_REGEX = /^[0-9]+$/
    PHN_REGEX = /^[0-9]{10}$/
    ADDR_REGEX = /^[a-zA-Z0-9\s.:-]+$/

    validateText(value: string, max: number, min: number) {
        if(value ==null || value.length < min || value.length > max)
            return false
        
        var matches = value.trim().match(this.TEXT_REGEX)
        console.log('Regex details ----> ', value, matches)
        if(matches == null)
            return false
        else if(matches.length == 1)
            return true
    }

    validateUserName(value: string, max: number, min: number) {
        if(value ==null || value.length < min || value.length > max)
        return false
    
        var matches = value.trim().match(this.USER_REGEX)
        console.log('Regex details ----> ', value, matches)
        if(matches == null)
            return false
        else if(matches.length == 1)
            return true
    }

    validateEmail(value: string, max: number, min: number) {
        if(value ==null || value.length < min || value.length > max)
            return false
        
        var matches = value.trim().match(this.EMAIL_REGEX)
        console.log('Regex details ----> ', value, matches)
        if(matches == null)
            return false
        else if(matches.length == 1)
            return true
    }

    validateNumber(value: string, max: number, min: number) {
        if(value ==null || value.length < min || value.length > max)
            return false
        
        var matches = value.trim().match(this.NUM_REGEX)
        console.log('Regex details ----> ', value, matches)
        if(matches == null)
            return false
        else if(matches.length == 1)
            return true
    }

    validatePhone(value: string, max: number, min: number) {
        if(value ==null || value.length < min || value.length > max)
            return false
        
        var matches = value.trim().match(this.PHN_REGEX)
        console.log('Regex details ----> ', value, matches)
        if(matches == null)
            return false
        else if(matches.length == 1)
            return true
    }

    validatePassword(value: string, max: number, min: number) {
        if(value ==null || value.length < min || value.length > max)
            return false
        
        var matches = value.trim().match(this.PASS_REGEX)
        console.log('Regex details ----> ', value, matches)
        if(matches == null)
            return false
        else if(matches.length == 1)
            return true
    }

    validateAddress(value: string, max: number, min: number) {
        if(value ==null || value.length < min || value.length > max)
            return false
        var matches = value.trim().match(this.ADDR_REGEX)
        console.log('Regex details ---> ', value, matches)

        if(matches == null)
            return false
        else if(matches.length == 1)
            return true
    }

}