import { Injectable } from '@angular/core';
import { UserService } from './user.service';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { ErrorService } from '../error/error-handler.service';
import { Customer } from '../../models/users/customer.model';
import { Address } from '../../models/users/address.model';
import { NotificationService } from '../notfications/notification.service';

@Injectable()
export class AddressService {
    
    ADDR_URL = 'addresses/'

    constructor(
        private userService: UserService,
        private http: HttpClient,
        private errorService: ErrorService,
        private notifService: NotificationService
    ) { }

    customerDetails: Customer;

    userAddresses: Address[] = [];
    addressLoadedObserver = new Subject<boolean>();


    fetchUserAddress() {
        if(this.userService.customerLoggedIn) {

            this.customerDetails = this.userService.getCustomerDetails();
            this.http.get(this.ADDR_URL + this.customerDetails.customerID).subscribe(
                (addresses: Address[]) => {
                    this.userAddresses = addresses;
                    console.log('User Addresses --> ', this.userAddresses)
                    this.addressLoadedObserver.next(true)
                },
                error => {
                    this.addressLoadedObserver.next(false);
                    this.errorService.logError('error occured loading customer addresses ', error);
                }
            )

        }
    }

    getUserAddresses() {
        let localAddr = JSON.parse(JSON.stringify(this.userAddresses));
        return localAddr;
    }

    
    saveAddress(address: Address) {
        var msg = 'Address updated failed !!'
        var type = this.notifService.NOTIF_WARNING
        this.http.post(this.ADDR_URL, address).subscribe(
            (address: Address) => {
                if(address != null) {
                    this.fetchUserAddress()
                    msg = 'Address update success !!'
                    type = this.notifService.NOTIF_SUCCESS
                }

                this.notifService.pushNotification(type, msg)
            },
            error => {
                this.errorService.logError('error occured loading customer addresses ', error);
            }
        )
    }

}