import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { UserService } from '../Utilities/services/users/user.service';
import { UILoaderService } from '../Utilities/services/ui-loader/ui-loader.service';
import { ProductService } from '../Utilities/services/products/product.service';
import { ActivatedRoute, Params } from '@angular/router';
import { Product } from '../Utilities/models/products/product.model';
import { GlobalModelsService } from '../Utilities/services/global/global-models.service';
import { ProductSearchService } from '../Utilities/services/products/product-search.service';

@Component({
  selector: 'app-product-blocks',
  templateUrl: './product-blocks.component.html',
  styleUrls: ['./product-blocks.component.css']
})
export class ProductBlocksComponent implements OnInit {

  query: string = '';

  section: string = '';
  categoryType: string = '';
  catergorySubType: string = '';

  allProducts: Product[] = [];
  allProductsLoaded = false;

  constructor(
    private route: ActivatedRoute,
    private uiLoaderService: UILoaderService,
    private productService: ProductService,
    private productSearchService: ProductSearchService
  ) {}

  ngOnInit(): void {
    this.route.queryParams.subscribe(
      (params: Params) => {
        console.log(params)
        this.query = params['query']
        this.section = params['section'];
        this.categoryType = params['categoryType'];
        this.catergorySubType = params['catergorySubType'];

        // UI Loader is started before requesting products from user
        console.log(this.uiLoaderService.apiLoadingScreen + ' set to ' + true)
        this.uiLoaderService.loaderObservable.next([this.uiLoaderService.apiLoadingScreen, true]);
        
        if(this.query == null || this.query.length < 4)
          this.productSearchService.searchForCriteria(this.section, this.categoryType, this.catergorySubType);
        else {
          this.productSearchService.searchForKeyword(this.query);
        }
      }
    )
    
    this.productService.allProductsObserver.subscribe(
      (allProductsLoadedLocal: boolean) => {
        this.allProducts = this.productService.getAllProducts();
        this.allProductsLoaded = allProductsLoadedLocal;

        
        if(this.allProducts.length == 0)
          this.allProductsLoaded = false

        // UI Loader is stopped as the response is received
        console.log(this.uiLoaderService.apiLoadingScreen + ' set to ' + false)
        this.uiLoaderService.loaderObservable.next([this.uiLoaderService.apiLoadingScreen, false]);
      }
    )

    this.productService.sortCurrentProductsObserver.subscribe(
      (sortCriteria: string) => {
        console.log('Sort products check ==> ', this.allProducts)
        if(this.allProducts != null && this.allProducts.length > 0) {
          
          // UI Loader is started before sort started on products
          console.log(this.uiLoaderService.apiLoadingScreen + ' set to ' + true)
          this.uiLoaderService.loaderObservable.next([this.uiLoaderService.apiLoadingScreen, true]);
          
          this.allProducts = this.productSearchService.sortProducts(this.allProducts, sortCriteria);
          
          // UI Loader is stopped as the products are sorted
          console.log(this.uiLoaderService.apiLoadingScreen + ' set to ' + false)
          this.uiLoaderService.loaderObservable.next([this.uiLoaderService.apiLoadingScreen, false]);
        }
      }
    )


  }

}
