import { Component, OnInit, Renderer2, Input } from '@angular/core';
import { Category } from 'src/app/Utilities/models/global-models/category.model';
import { SubCategory } from 'src/app/Utilities/models/global-models/sub-category.model';
import { ProductKartService } from 'src/app/Utilities/services/products/orders/product-kart.service';
import { Collection } from 'src/app/Utilities/models/global-models/collection.model';
import { Router } from '@angular/router';
import { ProductService } from 'src/app/Utilities/services/products/product.service';
import { UserService } from 'src/app/Utilities/services/users/user.service';
import { WishListService } from 'src/app/Utilities/services/products/orders/wish-list.service';

@Component({
  selector: 'app-bottom-header',
  templateUrl: './bottom-header.component.html',
  styleUrls: ['./bottom-header.component.css']
})
export class BottomHeaderComponent implements OnInit {

  
  searchTerm: string = '';

  activeHeader = 'home';
  hoveredHeader = 'home';

  bagCount = '00';
  wishlistCount = '00';

  productSearchActive = false;

  @Input() womens = new Map<Category, SubCategory[]>();
  @Input() mens = new Map<Category, SubCategory[]>();
  @Input() kids = new Map<Category, SubCategory[]>();

  @Input() womensLoaded: boolean;
  @Input() mensLoaded: boolean;
  @Input() kidsLoaded: boolean;
  
  @Input() collections: Collection[]

  @Input() collectionsLoaded: boolean;

  toolTipHtml = ''

  navbar: {head: string, load: boolean, data: Map<Category, SubCategory[]>}[] = [];
  isUserLoggedIn = false;

  constructor(
    private render: Renderer2,
    private productService: ProductService,
    private productKartService: ProductKartService,
    private userService: UserService,
    private wishListService: WishListService,
    private router: Router
  ) { }


  ngOnInit(): void {
    this.isUserLoggedIn = this.userService.customerLoggedIn

    this.userService.customerLoggedInObserver.subscribe(
      (localCustomerLoggedIn) => this.isUserLoggedIn = localCustomerLoggedIn
    )
    
    this.navbar.push(
      {head: 'womens', load: this.womensLoaded, data: this.womens},
      {head: 'mens', load: this.mensLoaded, data: this.mens},
      {head: 'kids', load: this.kidsLoaded, data: this.kids}
    )
    
    console.log('Local navbar ====> ', this.navbar)

    this.productService.allProductsObserver.subscribe(
      () => this.searchTerm = ''
    )

    if(this.productKartService.kartCount > 0) {
      this.setKartCount(this.productKartService.kartCount)
    }

    if(this.wishListService.wishListLoaded) {
      let localWishListCount = this.wishListService.getWishList().wishListProducts.length
      this.setWishListCount(localWishListCount)
    }

    this.productKartService.kartCountLoadedObserver.subscribe (
      (kartCount: number) => {
        this.setKartCount(this.productKartService.kartCount)
      }
    )

    this.wishListService.wishListLoadedObservable.subscribe(
      (wishListLoaded) => {
        if(wishListLoaded) {
          let localWishListCount = this.wishListService.getWishList().wishListProducts.length
          this.setWishListCount(localWishListCount)
        } else {
          this.wishlistCount = '00'
        }
      }
    )

  }

  setKartCount(localKartCount: number) {
    if(localKartCount < 10)
      this.bagCount = '0' + localKartCount.toString();
    else if(localKartCount >= 10)
      this.bagCount = localKartCount.toString()
    else
      this.bagCount = '00'
  }

  setWishListCount(wishListCount: number) {
    if(wishListCount < 10)
      this.wishlistCount = '0' + wishListCount.toString()
    else
      this.wishlistCount = wishListCount.toString()
  }

  /**
   * 
   * Please, DO NOT TOUCH THE BELOW METHOD !!!
   * 
   * If user hover on one of the navbar's, then
   * 1. Check if its not home, only perform operation on others
   * 2. check if the hovered nav item has data to open
   * 3. Then perform entire page operation for Womens, other for others 
   * 
   * @param status 
   */
  setHovered(type: string, status: boolean) {
    this.activeHeader = type
    if(type !== 'home') {
      const angleDown = document.getElementById(type + '-angle-down')
      const optionstab = document.getElementById(type);

      if(status) {
        this.hoveredHeader = type;
        this.render.setStyle(angleDown, 'transform', 'rotate(90deg)')
      }
      else {
        this.hoveredHeader = ''
        this.render.removeStyle(angleDown, 'transform',)
      }

      this.render.removeStyle(optionstab, 'height');
    }
  }

  /**
   * 
   * Please, DO NOT TOUCH THE BELOW METHOD !!!
   * 
   * If user hover on one of the navbar's, then
   * 1. Check if its not home, only perform operation on others
   * 2. check if the hovered nav item has data to open
   * 3. Check if the data is already open, perfrom differently when open & closed
   * 4. Then perform entire page operation for Womens, other for others 
   * 
   * @param status 
   */
  openOrCloseOptions(type: string) {
    this.activeHeader = type
    if(type !== 'home') {
      const optionstab = document.getElementById(type);
      const angleDown = document.getElementById(type + '-angle-down')
      if(type !== 'collections') {
        if(type === this.hoveredHeader) {
          this.render.setStyle(optionstab, "height", '0px');
          this.render.removeStyle(angleDown, 'transform',)
          this.hoveredHeader = ''
        } else {
          this.render.setStyle(optionstab, "height", '320px');
          this.render.setStyle(angleDown, 'transform', 'rotate(90deg)')
          this.hoveredHeader = type
        }
      } else {
        if(type === this.hoveredHeader) {
          this.render.setStyle(optionstab, "height", '0px');
          this.render.removeStyle(angleDown, 'transform',)
          this.hoveredHeader = ''
        } else {
          this.render.setStyle(optionstab, "height", '200px');
          this.render.setStyle(angleDown, 'transform', 'rotate(90deg)')
          this.hoveredHeader = 'collections'
        }
      }
    }
  }

  changeSearchBoxColor(status: boolean) {
    this.productSearchActive = status;
  }

  searchProducts() {
    if(this.searchTerm != null && this.searchTerm.length >= 4) {
      this.router.navigate(['/', 'ui-products', 'search'], {queryParams: {query: this.searchTerm}})
    }
  }

  setTooltip(message: string) {
    this.toolTipHtml = message
  }

}
