import { Injectable } from '@angular/core';
import { Category } from 'src/app/Utilities/models/global-models/category.model';
import { Section } from 'src/app/Utilities/models/global-models/section.model';
import { SubCategory } from 'src/app/Utilities/models/global-models/sub-category.model';
import { Collection } from 'src/app/Utilities/models/global-models/collection.model';
import { Discount } from 'src/app/Utilities/models/global-models/discount.model';
import { Material } from 'src/app/Utilities/models/global-models/material.model';
import { MetalPurity } from 'src/app/Utilities/models/global-models/metal-purity.model';
import { LandingPage } from '../../models/landing-page/landing-page.model';
import { Subject } from 'rxjs';

@Injectable()
export class GlobalModelsService {

    categories: Category[] = []
    subCategory: SubCategory[] =[]
    collections: Collection[] = []
    discounts: Discount[] = []
    materials: Material[] = []
    metalPurities: MetalPurity[] = []
    sections: Section[] = []

    globalModelsLoaded = false
    globalModelsLoadedObservable = new Subject<boolean>();

    constructor(

    ){}

    
    assignGlobalModels(landingPage: LandingPage) {
        this.categories = landingPage.globalCategoryList
        this.collections = landingPage.globalCollectionList
        this.sections = landingPage.globalSectionList
        this.subCategory = landingPage.globalSubCategory
        this.materials = landingPage.globalMaterialList
        this.metalPurities = landingPage.globalMetalPurityList
        this.discounts = landingPage.globalDiscountList

        if(this.categories == null || this.categories.length == 0)
            throw new Error('Categories are null')

        if(this.sections == null || this.categories.length == 0)
            throw new Error('Sections are null')

        if(this.subCategory == null || this.categories.length == 0)
            throw new Error('sub categories are null')

        this.globalModelsLoaded = true
        this.globalModelsLoadedObservable.next(this.globalModelsLoaded)

        console.log('All Global Lists ===> ', this.categories, this.subCategory, this.sections, this.collections, this.metalPurities, this.materials, this.discounts)

    }

    getCategoryByID(categoryID: number): Category {
        let category = null
        this.categories.forEach(
            c => {
                if(c.categoryID == categoryID) category = c
            }
        )
        return category;
    }

    getSubCategoryByID(subCategoryID: number): SubCategory {
        let subCategory = null
        this.subCategory.forEach(
            sc => {
                if(sc.subCategoryID == subCategoryID) subCategory = sc
            }
        )
        return subCategory;
    }

    getCollectionByID(collectionID: number): Collection {
        let collection = null
        this.collections.forEach(
            c => {
                if(c.CollectionID == collectionID) collection = c
            }
        )
        return collection
    }

    getDiscountByID(discountID: number): Discount {
        let discount = null
        this.discounts.forEach(
            d => {
                if(d.discountID == discountID) discount = d
            }
        )
        return discount;
    }

    getMaterialByID(materialID: number): Material {
        let material = null
        this.materials.forEach(
            m => {
                if(m.materialID == materialID) material = m
            }
        )
        return material
    }

    getMetalPurityByID(metalPurityID: number): MetalPurity {
        let metalPurity = null
        this.metalPurities.forEach(
            m => {
                if(m.metalPurityID == metalPurityID) metalPurity = m
            }
        )
        return metalPurity;
    }

    getSectionByID(sectionID: number): Section {
        let section = null
        this.sections.forEach(
            s => {
                if(s.sectionID == sectionID) section = s
            }
        )
        return section;
    }

    getCategoryNamesAsList(): string[] {
        let categoryNames = []
        this.categories.forEach(
            c => categoryNames.push(c.categoryName)
        )
        return categoryNames
    }

    getSubCategoryNamesAsList(): string[] {
        let subCategoryNames = []
        this.subCategory.forEach(
            sc => subCategoryNames.push(sc.subCategoryName)
        )
        return subCategoryNames;
    }

    getCollectionNamesAsList(): string[] {
        let collectionNames = []
        this.collections.forEach(
            c => collectionNames.push(c.collectionName)
        )
        return collectionNames;
    }

    getDiscountPercentsAsList(): string[] {
        let discountList = []
        this.discounts.forEach(
            d => discountList.push(d.discountType)
        )
        return discountList
    }

    getMaterialNamesAsList(): string[] {
        let materialNames = []
        this.materials.forEach(
            m => materialNames.push(m.materialType)
        )
        return materialNames
    }

    getMetalPurtiesAsList(): string[] {
        let metalPurityNames = []
        this.metalPurities.forEach(
            m => metalPurityNames.push(m.purity)
        )
        return metalPurityNames;
    }

    getSectionNamesAsList(): string[] {
        let sectionName = []
        this.sections.forEach(
            s => sectionName.push(s.sectionName)
        )
        return sectionName;
    }

    getSubCategoriesBasedOnCategoryName(categoryName: string): string[] {
        let subCategories = []
        this.subCategory.forEach(
            s => {
                let c = this.getCategoryByID(s.categoryID)
                if(c.categoryName === categoryName)
                    subCategories.push(s.subCategoryName)
            }
        )
        return subCategories;
    }

}