import { Component, OnInit } from '@angular/core';
import { UILoaderService } from '../Utilities/services/ui-loader/ui-loader.service';
import { CarouselImage } from '../Utilities/models/landing-page/carousel_image.model';
import { Category } from '../Utilities/models/global-models/category.model';
import { ProductsCombo } from '../Utilities/models/landing-page/products-combo.model';
import { LandingPageService } from '../Utilities/services/landing-page/landing-page.service';
import { LandingPage } from '../Utilities/models/landing-page/landing-page.model';
import { Product } from '../Utilities/models/products/product.model';

@Component({
  selector: 'app-home-blocks',
  templateUrl: './home-blocks.component.html',
  styleUrls: ['./home-blocks.component.css']
})
export class HomeBlocksComponent implements OnInit {

  constructor(
    private landingPageService: LandingPageService,
    private uiLoaderService: UILoaderService
  ) { }

  landingPage: LandingPage = null;

  
  carouselImagesLoaded = false;
  carouselImages: CarouselImage[] = [];

  categoryModelsLoaded = false;
  popularCategories: Category[] = [];

  newArrivalsLoaded = false;
  newArrivals: Product[] = [];
  newArrivalsComboLoaded = false;
  newArrivalsCombo: ProductsCombo[]= [];

  bestSellingLoaded = false;
  bestSellers: Product[] = [];

  topProductsLoaded = false;
  topProducts: Product[] = [];

  ngOnInit(): void {

    this.landingPageService.landingPageLoaded.subscribe(
      (localLoaded) => {

        if(localLoaded) {
          this.setAllModels()
        }

      }
    )

    this.setAllModels()

  }

  setAllModels() {
    this.landingPage = this.landingPageService.getLandingPage();

    if(this.landingPage != null) {

      var welcomeCarousel = this.landingPage.welcomePageCarousel
      if(welcomeCarousel != null) {
        this.carouselImages = this.landingPage.welcomePageCarousel
        this.carouselImagesLoaded = true
      }

      var popularcategories = this.landingPage.popularCategories
      if(popularcategories != null) {
        this.popularCategories = popularcategories
        this.categoryModelsLoaded = true
      }

      var newArrivals = this.landingPage.newArrivals
      if(newArrivals != null) {
        this.newArrivals = newArrivals
        this.newArrivalsLoaded = true
      }

      var productsCombo = this.landingPage.combos
      if(productsCombo != null) {
        this.newArrivalsCombo = productsCombo
        this.newArrivalsComboLoaded = true
      }

      var topProducts = this.landingPage.topProducts
      if(topProducts != null) {
        this.topProducts = topProducts
        this.topProductsLoaded = true
      }
      
      var bestSellers = this.landingPage.bestSellers
      if(bestSellers != null) {
        this.bestSellers = bestSellers
        this.bestSellingLoaded = true
      }
      
    }
  }

}
